import { chromeLocalStorage } from "./chromeStorage";

export const CACHE_CONFIG = {
  REMOTE_CONFIG: {
    cacheTime: 60 * 60 * 1000 * 4, // 4 hours
    cacheKey: "remoteConfig",
  },
  FETCH_CANDIDATE_DATA: {
    // This is just used to call the api once on the widget load and not multiple times
    // since data is used multiple times and no context or central store to refrence its value from there
    cacheTime: 15 * 1000, // 15 sec
    cacheKey: "candidateData",
  },
  LI_ME_DATA: {
    // This is just used to call the api once not multiple times in a 30 sec window
    cacheTime: 30 * 1000, // 30 sec
    cacheKey: "liMeData",
  },
};

export const getCachedResponse = async (apiCall, config) => {
  if (!config?.cacheKey || !config?.cacheTime) return null;
  const resultsStorageKey = `${config.cacheKey}-results`;
  const lastCalledStorageKey = `${config.cacheKey}-last-called`;

  const [lastApiCallTime, cachedResults] = await Promise.all([
    chromeLocalStorage.getItemAsync(lastCalledStorageKey),
    chromeLocalStorage.getItemAsync(resultsStorageKey),
  ]);

  // Check if there is a cache data which has not expired
  if (
    lastApiCallTime &&
    lastApiCallTime + config.cacheTime >= Date.now() &&
    cachedResults
  ) {
    return cachedResults;
  }

  // No cache data found make a new api call
  const data = await apiCall();
  if (data) {
    await Promise.all([
      chromeLocalStorage.setItemAsync(lastCalledStorageKey, Date.now()),
      chromeLocalStorage.setItemAsync(resultsStorageKey, data),
    ]);
    return data;
  }

  return null;
};
