import { getFirebaseRemoteConfigObj } from "../api/internalServices";
import {
  CS_CIRCLES_ORG_IDS,
  CS_EXTENSION_USER_MODE,
  CS_INSTALLATION_PARAMS,
  CS_NUMBER_OF_APPLIES,
  CS_UID,
} from "../constants/chromeStorageConstants";
import {
  EXTENSION_USER_MODE,
  EXTENSION_USER_TYPE,
  SYNC_CONFIG_KEY,
} from "../constants/commonConstants";
import { chromeLocalStorage } from "./chromeStorage";
import { CACHE_CONFIG, getCachedResponse } from "./cacheApiResponse";
import { checkArraySanity, checkObjectSanity } from "./lodash";

const isUserTypeCandidate = async () => {
  return (
    (
      await chromeLocalStorage.getItemAsync(CS_INSTALLATION_PARAMS)
    )?.extensionUserType?.toUpperCase() === EXTENSION_USER_TYPE.CANDIDATE
  );
};

const removeTrailingSlash = (inputString) => {
  if (inputString.endsWith("/")) {
    // If the string ends with "/", remove the last character
    return inputString.slice(0, -1);
  }
  // If there is no trailing "/", return the original string
  return inputString;
};

const getSyncConfig = async () => {
  const config = self.SYNC_CONFIG;
  if (checkObjectSanity(config)) return config;

  const resultsStorageKey = `${CACHE_CONFIG.REMOTE_CONFIG.cacheKey}-results`;
  const cachedResult = await chromeLocalStorage.getItemAsync(resultsStorageKey);

  if (checkObjectSanity(cachedResult)) {
    try {
      const syncConfig = JSON.parse(
        cachedResult?.[SYNC_CONFIG_KEY]?.defaultValue?.value
      );
      if (checkObjectSanity(syncConfig)) {
        self.SYNC_CONFIG = syncConfig;
        return syncConfig;
      }
    } catch (_) {}
  }
  return {};
};

self.fetchingRemoteConfig = false;

const fetchSyncConfig = async () => {
  self.fetchingRemoteConfig = true;
  try {
    const remoteConfig = await getCachedResponse(
      getFirebaseRemoteConfigObj,
      CACHE_CONFIG.REMOTE_CONFIG
    );
    const syncConfig = JSON.parse(
      remoteConfig?.[SYNC_CONFIG_KEY]?.defaultValue?.value
    );
    if (checkObjectSanity(syncConfig)) {
      self.SYNC_CONFIG = syncConfig;
    }
  } catch (err) {
    const configLastCalledKey = `${CACHE_CONFIG.REMOTE_CONFIG.cacheKey}-last-called`;
    chromeLocalStorage.setItemAsync(configLastCalledKey);
  }
  self.fetchingRemoteConfig = false;
};

// To update remote config for sync purpose
const checkAndFetchLatestRemoteConfig = async () => {
  const config = CACHE_CONFIG.REMOTE_CONFIG;
  const resultsStorageKey = `${config.cacheKey}-results`;
  const lastCalledStorageKey = `${config.cacheKey}-last-called`;

  const [lastApiCallTime, cachedResults] = await Promise.all([
    chromeLocalStorage.getItemAsync(lastCalledStorageKey),
    chromeLocalStorage.getItemAsync(resultsStorageKey),
  ]);

  // Check if there is a cache data which has not expired
  if (
    (lastApiCallTime &&
      lastApiCallTime + config.cacheTime >= Date.now() &&
      checkObjectSanity(cachedResults)) ||
    self.fetchingRemoteConfig
  ) {
    return;
  }
  await fetchSyncConfig();
};

const sleep = (delay = 0) => {
  return new Promise((res) => {
    setTimeout(() => res(), delay);
  });
};

const getImageAssetUrl = (imageName) =>
  chrome.runtime.getURL(`assets/images/${imageName}`);

const getRandomNumberInRange = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1)) + min;
};

const incrementAppliedApplications = async (count) => {
  const applyCount = await chromeLocalStorage.getItemAsync(
    CS_NUMBER_OF_APPLIES
  );
  const totalCount = (applyCount || 0) + count;
  await chromeLocalStorage.setItemAsync(CS_NUMBER_OF_APPLIES, totalCount);
  return totalCount;
};

async function getExtensionConfigForSync(key, defaultValue, isNumeric = false) {
  const extensionUserMode = await chromeLocalStorage.getItemAsync(
    CS_EXTENSION_USER_MODE
  );
  const uid = await chromeLocalStorage.getItemAsync(CS_UID);
  const circleOrg = await chromeLocalStorage.getItemAsync(CS_CIRCLES_ORG_IDS);
  const config = await getSyncConfig();

  if (
    checkObjectSanity(config?.uidOverrides) &&
    config?.uidOverrides?.[uid]?.[key] !== undefined
  ) {
    const value = config?.uidOverrides?.[uid]?.[key];
    if (isNumeric && isNaN(value)) return defaultValue;
    return value;
  }
  if (
    extensionUserMode === EXTENSION_USER_MODE.CIRCLES &&
    checkObjectSanity(config?.orgIdOverrides) &&
    checkArraySanity(circleOrg)
  ) {
    const foundOrg = circleOrg.find((org) =>
      Object.keys(config?.orgIdOverrides).includes(org)
    );
    if (config?.orgIdOverrides?.[foundOrg]?.[key] !== undefined) {
      const value = config?.orgIdOverrides?.[foundOrg]?.[key];
      if (isNumeric && isNaN(value)) return defaultValue;
      return value;
    }
  }
  if (
    extensionUserMode === EXTENSION_USER_MODE.CIRCLES &&
    config?.[EXTENSION_USER_MODE.CIRCLES]?.[key] !== undefined
  ) {
    const value = config?.[EXTENSION_USER_MODE.CIRCLES]?.[key];
    if (isNumeric && isNaN(value)) return defaultValue;
    return value;
  }
  const value = config?.[EXTENSION_USER_MODE.DEFAULT]?.[key] || defaultValue;
  if (isNumeric && isNaN(value)) return defaultValue;
  return value;
}

export {
  isUserTypeCandidate,
  removeTrailingSlash,
  sleep,
  getImageAssetUrl,
  getRandomNumberInRange,
  incrementAppliedApplications,
  fetchSyncConfig,
  getExtensionConfigForSync,
  checkAndFetchLatestRemoteConfig,
};
