import React, { useCallback, useMemo, useState } from "react";
import { STICKY_SIDE } from "../../constants/commonConstants";
import "./styles.css";
import SaveIcon from "../../assets/svgs/save";
import DotsLoader from "../Loaders/DotsLoader";
import SavedIcon from "../../assets/svgs/saved";
import { EXTENSION_WEBSITE_DOMAIN } from "../../constants/urls";
import { checkObjectSanity } from "../../utils/lodash";

const SaveJob = ({ stickySide, saved: savedJobData, onSave }) => {
  const [isHovered, setIsHovered] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  const handleMouseEnter = useCallback(() => {
    setIsHovered(true);
  }, []);

  const handleMouseLeave = useCallback(() => {
    setIsHovered(false);
  }, []);

  const isSaved = checkObjectSanity(savedJobData);

  const handleClick = useCallback(
    (e) => {
      if (isLoading || isSaved) return;
      setIsLoading(true);
      const target = e.currentTarget;
      target.classList.add("active");
      onSave(() => {
        setIsLoading(false);
        target.classList.remove("active");
      });
      setIsHovered(false);
    },
    [isSaved, isLoading, onSave]
  );

  const textContent = useMemo(
    () => <p className="save-icon--text pointer">Save job and apply later</p>,
    []
  );

  const textContentUnsave = useMemo(
    () => (
      <p className="save-icon--text">
        <a
          className="saved-link"
          href={`${EXTENSION_WEBSITE_DOMAIN}/applied?tab=saved`}
          target="_blank"
          rel="noreferrer"
        >
          View saved jobs
        </a>
      </p>
    ),
    []
  );

  return (
    <div
      className="save-icon-wrapper"
      style={
        isHovered
          ? {
              transform: `translateX(${
                stickySide === STICKY_SIDE.LEFT ? "65px" : "-65px"
              })`,
            }
          : {}
      }
    >
      <div
        className={`save-icon ${isHovered ? "save-icon-slide" : ""} ${
          isLoading ? "not-allowed" : ""
        }`}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        onClick={handleClick}
      >
        {isHovered && stickySide === STICKY_SIDE.LEFT
          ? isSaved
            ? textContentUnsave
            : textContent
          : null}
        {isLoading ? (
          <DotsLoader color="rgb(55, 5, 46)" />
        ) : (
          <div className="save-icon-container">
            {isSaved ? (
              <SavedIcon className="save-icon-svg" />
            ) : (
              <SaveIcon className="save-icon-svg" />
            )}
          </div>
        )}
        {isHovered && stickySide === STICKY_SIDE.RIGHT
          ? isSaved
            ? textContentUnsave
            : textContent
          : null}
      </div>
    </div>
  );
};

export default SaveJob;
