import { BASE_URL } from "./constants";
import fetchAdapter from "./fetchAdapter";

export const saveLinkedInJobsFromExtension = async (data) => {
  return fetchAdapter(`${BASE_URL}/adhoc/saveLinkedInJobsFromExtension`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: data,
  });
};

export const saveAIApplyScrapedJobs = async (data) => {
  return fetchAdapter(`${BASE_URL}/adhoc/saveAIApplyScrapedJobs`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: data,
  });
};
