// This will contain methods which will transform the backend data and fill in the fields during autoApply

import { checkArraySanity } from "../../utils/lodash";

export const autoApplyUtilityMethods = {
  splitOnComaAndGetFirstEntry: (companiesString) =>
    companiesString?.split?.(",")?.[0] ?? "",
  resumeFormat: (resumeText, resumeUrl) => {
    if (!resumeText || !resumeUrl) return "";
    return `
    ==================================================================
    Actual resume can be downloaded from ${resumeUrl}
    ==================================================================

    ${resumeText}
    `;
  },
  concatWithSpace: (...texts) => {
    if (!checkArraySanity(texts)) return "";
    return texts.join(" ");
  },
  minFullyCompletedEperienceInYears: (relevantExp) => {
    if (typeof relevantExp !== "number") return "";
    return Math.floor(relevantExp);
  },
  remainingCompletedEperienceInMonths: (relevantExp) => {
    if (typeof relevantExp !== "number") return "";
    return Math.floor((relevantExp % 1) * 12);
  },
  default: (data) => data?.toString(),
};
