import { createRoot } from "react-dom/client";

export const attachReactComponentToDom = (component) => {
  // Create a shadow-root parent
  let extensionHost = document.getElementById("weekday-host");
  if (!extensionHost) {
    extensionHost = document.createElement("div");
    extensionHost.setAttribute("id", "weekday-host");
    document.body.append(extensionHost);
    extensionHost.attachShadow({ mode: "open" });
  }

  const shadowRoot = extensionHost.shadowRoot;
  const root = createRoot(shadowRoot);
  root.render(component);
};

export const shadowDomQuerySelector = (query) => {
  const extensionHost = document.getElementById("weekday-host");
  if (!extensionHost) return null;

  return extensionHost.shadowRoot.querySelector(query);
};
