import { BASE_URL } from "./constants";
import fetchAdapter from "./fetchAdapter";

export const getAutoApplyDataForCandidate = async (
  htmlSnippet,
  publicIdentifier,
  userPrompt
) => {
  return fetchAdapter(
    `${BASE_URL}/candidate/getAutoApplyDataForCandidate`,
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: {
        htmlSnippet,
        publicIdentifier,
        userPrompt,
      },
    },
    60
  );
};
