import * as Sentry from "@sentry/browser";
import { ENABLE_ENCRYPTION } from "./constants";
import { sendEvent } from "../analytics/analytics";
import { SCREEN_EVENTS } from "../analytics/event";
import { encryptData, decryptData } from "../utils/encryption";
import packageJson from "../../package.json";
import { DISABLED_ERROR_MESSAGES } from "../sentry/initialize";
import {
  BLACKLISTED_URL_FOR_MIXPANEL,
  FETCH_ERROR,
  SKIP_REFETCH_APIS,
} from "../constants/common";
import { chromeLocalStorage } from "../utils/chromeStorage";
import { CS_SESSION_ID } from "../constants/chromeStorageConstants";
import { handleSessionTokenRefresh } from "../Background/security";

const MAX_ATTEMPTS = 3;

const fetchAdapter = async (url, options, customTimeout = 30) => {
  const timeout = isNaN(customTimeout) ? 30 : customTimeout;
  let body = options?.body;
  if (["POST", "PUT", "DELETE"].includes(options?.method) && body) {
    // ENABLE_ENCRYPTION will be replaced by webpack
    body = ENABLE_ENCRYPTION ? encryptData(body) : body;
  }

  let attempt = 0;
  let timeoutId;
  while (attempt < MAX_ATTEMPTS) {
    try {
      const sessionId = await chromeLocalStorage.getItemAsync(CS_SESSION_ID);
      const controller = new AbortController();
      timeoutId = setTimeout(() => {
        controller.abort("Timeout");
        if (
          BLACKLISTED_URL_FOR_MIXPANEL.some((blacklistedUrl) =>
            url.endsWith(blacklistedUrl)
          )
        ) {
          return;
        }
        sendEvent(SCREEN_EVENTS.API_TIMEOUT, {
          url,
        });
        return Promise.reject({ message: "Timeout" });
      }, timeout * 1000); // 30 seconds timeout

      const result = await fetch(url, {
        ...options,
        headers: {
          ...options.headers,
          "Extension-Version": packageJson?.version,
          "Extension-Installed-Version": packageJson?.intVersion,
          "Extension-Id": "figlaioomchnkcajbbkfdgjgfdkfgmdg",
          ...(sessionId && {
            authorization: `Bearer ${sessionId}`,
          }),
        },
        signal: controller.signal,
        body: body
          ? JSON.stringify(ENABLE_ENCRYPTION ? { payload: body } : body)
          : undefined,
      });
      clearTimeout(timeoutId);
      if (result.status === 401) {
        // Refresh token
        if (attempt === 0) {
          attempt = 2;
          await handleSessionTokenRefresh();
        } else {
          return Promise.reject(result);
        }
      } else if (result.ok) {
        const data = await result.json();
        if (ENABLE_ENCRYPTION) {
          const decryptedData = decryptData(data);
          return decryptedData;
        }
        return data;
      } else {
        return Promise.reject(result);
      }
    } catch (err) {
      clearTimeout(timeoutId);
      const { headers, method } = options || {};
      if (
        FETCH_ERROR.some((errorMessage) =>
          err?.message?.toLowerCase().includes(errorMessage?.toLowerCase())
        ) &&
        !SKIP_REFETCH_APIS.some((endpoint) => url.endsWith(endpoint))
      ) {
        attempt += 1;
      } else {
        attempt = MAX_ATTEMPTS;
        if (
          !DISABLED_ERROR_MESSAGES.some((errorMessage) =>
            err?.message?.toLowerCase().includes(errorMessage?.toLowerCase())
          ) &&
          !url.includes("preview-") &&
          !url.includes("dev")
        ) {
          Sentry.captureException(new Error(`API Error in ${url}`), {
            tags: {
              apiEndpoint: url,
              extensionVersion: packageJson?.version,
            },
            extra: {
              body: JSON.stringify(options?.body),
              originalError: err?.message,
              headers: JSON.stringify(headers),
              method,
              encryptedPayload: body,
            },
          });
        }
        throw err;
      }
    }
  }
  return Promise.reject({ message: "Something went wrong" });
};

export default fetchAdapter;
