import React, {
  forwardRef,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from "react";
import Sidebar from "../Sidebar";
import {
  EXTENSION_WEBSITE_DOMAIN,
  EXTENSION_WEBSITE_URL,
  WEEKDAY_WEBSITE_DOMAIN,
} from "../../constants/urls";
import {
  CLOSE_PDF_PREVIEW,
  CLOSE_RESUME_BUILDER,
  CLOSE_RESUME_TAILORED_SIDEBAR,
  EDIT_TAILORED_RESUME,
  PREVIEW_RESUME,
  UPDATE_TAILORED_RESUME_DATA,
} from "../../constants/messageConstants";
import { CS_CANDIDATE_ID } from "../../constants/chromeStorageConstants";
import packageJson from "../../../package.json";
import DotsLoader from "../Loaders/DotsLoader";
import "./styles.css";

const TailoredResumeSidebar = forwardRef(
  ({ candidateId, closeSidebar, isSidebarOpen, onLoad, sessionId }, ref) => {
    const [editTailoredResume, setEditTailoredResume] = useState(false);
    const [pdf, setPdf] = useState("");
    const [showIframeLoading, setShowIframeLoading] = useState(true);
    const [resumeData, setResumeData] = useState({});
    const [editId, setEditId] = useState("");

    const iframeSrc = useMemo(() => {
      const url = new URL(`${EXTENSION_WEBSITE_URL}/resumeTailored`);
      url.searchParams.append("skipRemoteConfig", true);
      url.searchParams.append("sessionId", sessionId);
      url.searchParams.append(CS_CANDIDATE_ID, candidateId);
      url.searchParams.append("extensionVersion", packageJson?.version);
      return url.toString();
    }, [candidateId, sessionId]);

    const resumeBuilderSrc = useMemo(() => {
      const url = new URL(`${WEEKDAY_WEBSITE_DOMAIN}/resume-tailored`);
      url.searchParams.append(
        "resumeData",
        Buffer.from(JSON.stringify(resumeData)).toString("base64")
      );
      url.searchParams.append("template", "Academic");
      url.searchParams.append("sessionId", sessionId);
      url.searchParams.append(CS_CANDIDATE_ID, candidateId);
      url.searchParams.append("extensionVersion", packageJson?.version);
      url.searchParams.append("builderEditId", editId);
      return url.toString();
    }, [resumeData, candidateId, sessionId, editId]);

    useEffect(() => {
      // Communication with iframe
      const messageListener = (event) => {
        if (
          event.origin !== EXTENSION_WEBSITE_DOMAIN &&
          event.origin !== WEEKDAY_WEBSITE_DOMAIN
        ) {
          return;
        }

        switch (event?.data?.type) {
          case CLOSE_RESUME_TAILORED_SIDEBAR: {
            closeSidebar();
            break;
          }

          case EDIT_TAILORED_RESUME: {
            setResumeData(event?.data?.data?.resumeData);
            setEditId(event?.data?.data?.id);
            setEditTailoredResume(true);
            break;
          }

          case CLOSE_RESUME_BUILDER: {
            setEditTailoredResume(false);
            setShowIframeLoading(true);
            break;
          }

          case PREVIEW_RESUME: {
            setShowIframeLoading(true);
            setPdf(event?.data?.data?.link);
            break;
          }

          case UPDATE_TAILORED_RESUME_DATA: {
            if (event?.data?.data?.close) {
              setEditTailoredResume(false);
              setShowIframeLoading(false);
            }
            setResumeData(event?.data?.data?.resumeData);
            const message = {
              type: UPDATE_TAILORED_RESUME_DATA,
              data: event?.data?.data,
            };
            ref.current.contentWindow.postMessage(message, "*");
            break;
          }

          case CLOSE_PDF_PREVIEW: {
            setPdf("");
            setShowIframeLoading(false);
            break;
          }
        }
      };

      window.addEventListener("message", messageListener);

      return () => {
        window.removeEventListener("message", messageListener);
      };
    }, [closeSidebar, ref]);

    const onEditorLoad = useCallback(() => {
      setShowIframeLoading(false);
    }, []);

    const hideSidebar = editTailoredResume || Boolean(pdf);

    return (
      <Sidebar
        isOpen={isSidebarOpen}
        style={{
          right: isSidebarOpen ? 0 : "-500px",
          width: hideSidebar && isSidebarOpen ? "80%" : "400px",
        }}
      >
        <div className="resume-builder-container">
          {editTailoredResume ? (
            <div className="resume-builder-iframe-container">
              <iframe
                onLoad={onEditorLoad}
                width="100%"
                height="100%"
                src={resumeBuilderSrc}
                id="tailored-resume-editor"
                allow="clipboard-read; clipboard-write"
              />
              {showIframeLoading && (
                <div className="loading-container">
                  <DotsLoader color="rgb(55, 5, 46)" />
                </div>
              )}
            </div>
          ) : null}
          {Boolean(pdf) ? (
            <div className="resume-builder-iframe-container">
              <iframe
                width="100%"
                height="100%"
                src={`${EXTENSION_WEBSITE_URL}/pdfPreview?url=${pdf}`}
                id="tailored-resume-preview"
                onLoad={onEditorLoad}
                allow="clipboard-read"
              />
              {showIframeLoading && (
                <div className="loading-container">
                  <DotsLoader color="rgb(55, 5, 46)" />
                </div>
              )}
            </div>
          ) : null}
          <iframe
            onLoad={onLoad}
            width={hideSidebar ? "400px" : "100%"}
            height="100%"
            src={iframeSrc}
            ref={ref}
            id="tailored-resume-sidebar"
            allow="clipboard-read"
            style={{
              display: hideSidebar ? "none" : "block",
            }}
          />
        </div>
      </Sidebar>
    );
  }
);

TailoredResumeSidebar.displayName = "TailoredResumeSidebar";

export default TailoredResumeSidebar;
