export const isDateEqualAndMoreThanXSecondsOld = (date, seconds = 1) => {
  const currentDate = new Date();
  const timeDifference = currentDate - date;
  const totalMillisecondsInXDays = 1000 * seconds;

  return timeDifference >= totalMillisecondsInXDays;
};

export const getCurrentTime = () => {
  const now = new Date();

  // Get hours, minutes, and seconds
  let hours = now.getHours();
  let minutes = now.getMinutes();
  let seconds = now.getSeconds();

  // Determine AM/PM
  const meridiem = hours >= 12 ? "PM" : "AM";

  // Convert hours to 12-hour format
  hours = hours % 12;
  hours = hours ? hours : 12; // Handle midnight (0 hours)

  // Add leading zeros to single-digit minutes and seconds
  minutes = padZero(minutes);
  seconds = padZero(seconds);

  // Format the time as hh:mm:ss am/pm
  const formattedTime = `${hours}: ${minutes}: ${seconds} ${meridiem}`;

  return formattedTime;
};

// Function to add leading zero to single-digit numbers
function padZero(number) {
  return (number < 10 ? "0" : "") + number;
}
