import {
  CS_SELF_PUBLIC_ID,
  CS_UID,
} from "../../constants/chromeStorageConstants";
import { extractHTMLAsText } from "../../ContentScript/utils/autoApplyGPTUtils";
import { chromeLocalStorage } from "../../utils/chromeStorage";

export const getPageDataForResume = async () => {
  const [uid, publicIdentifier] = await Promise.all([
    chromeLocalStorage.getItemAsync(CS_UID),
    chromeLocalStorage.getItemAsync(CS_SELF_PUBLIC_ID),
  ]);
  const jobDescription = extractHTMLAsText();
  return {
    jobDescription,
    uid,
    publicIdentifier,
    url: window.location.href,
  };
};
