import { BASE_URL } from "./constants";
import fetchAdapter from "./fetchAdapter";

export const sendAnalyticsEvent = ({
  eventName,
  eventProps,
  distinctId,
  project,
}) => {
  const url = `${BASE_URL}/analytics/sendAnalyticsEvent`;

  return fetchAdapter(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: { project, eventName, eventProps, distinctId },
  });
};

export const analyticsIdentifyUser = ({
  userProperties,
  distinctId,
  project,
}) => {
  const url = `${BASE_URL}/analytics/identifyUser`;

  return fetchAdapter(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: { project, userProperties, distinctId },
  });
};
