import cryptoJS from "crypto-js";
import { DECRYPT_BODY_KEY, ENCRYPT_PAYLOAD_KEY } from "../api/constants";

export const decryptData = (crypticMessage) => {
  const bytes = cryptoJS.AES.decrypt(crypticMessage, DECRYPT_BODY_KEY);
  const decipherCrypt = bytes.toString(cryptoJS.enc.Utf8);
  return JSON.parse(decipherCrypt);
};

export const encryptData = (body) => {
  return cryptoJS.AES.encrypt(
    JSON.stringify(body),
    ENCRYPT_PAYLOAD_KEY
  ).toString();
};
