import { getBrowserNameAndVersion } from "../analytics/analytics";
import { CS_EXTENSION_USER_MODE } from "../constants/chromeStorageConstants";
import { EXTENSION_USER_MODE } from "../constants/commonConstants";
import {
  LINKEDIN_SIDEBAR_TOGGLE,
  OPEN_EASY_APPLY_SUCCESS,
  SHOW_TOAST_ON_WEBSITE,
} from "../constants/messageConstants";
import { EXTENSION_WEBSITE_URL, LINKEDIN_FEED_URL } from "../constants/urls";
import { chromeLocalStorage } from "../utils/chromeStorage";

export const openLinkedinPopup = async (openInNewPage) => {
  if (openInNewPage) {
    const tab = await chrome.tabs.create({
      url: LINKEDIN_FEED_URL,
      active: true,
    });
    setTimeout(() => {
      toggleLinkedinSidebar(tab.id);
    }, 1500);
  } else {
    chrome.tabs.query({ active: true, currentWindow: true }, function (tabs) {
      const tab = tabs?.[0];
      if (tab) {
        if (!tab.url?.startsWith("https://www.linkedin.com")) {
          chrome.tabs.update(tab.id, { url: LINKEDIN_FEED_URL });
          setTimeout(() => {
            toggleLinkedinSidebar(tab.id);
          }, 1500);
        } else {
          toggleLinkedinSidebar(tab.id);
        }
      }
    });
  }
};

export function openWebPage(url, retainExtension = false) {
  chrome.tabs.query({ active: true, currentWindow: true }, function (tabs) {
    const tab = tabs[0];
    chrome.tabs.update(tab.id, { url }, async function () {
      if (retainExtension) {
        if (!tab.url.toString().includes("/in/"))
          await chromeLocalStorage.removeItemAsync("lastVisitedProfile");

        setTimeout(() => {
          toggleLinkedinSidebar(tab.id);
        }, 1500);
      }
    });
  });
}

export async function openExtensionPage(targetUrl) {
  const isCircles =
    (await chromeLocalStorage.getItemAsync(CS_EXTENSION_USER_MODE)) ===
    EXTENSION_USER_MODE.CIRCLES;
  const url = `${EXTENSION_WEBSITE_URL}?openPage=${
    isCircles ? "contactSection" : "home"
  }`;
  chrome.tabs.create({
    url: targetUrl || url,
    active: true,
  });
}

export function openCookiesSetting() {
  const data = getBrowserNameAndVersion();
  let tabUrl;
  if (data.browser == "Chrome") {
    tabUrl = "chrome://settings/cookies";
  }

  if (data.browser == "Edge") {
    tabUrl = "edge://settings/content/cookies";
  }

  if (data.browser == "Brave") {
    tabUrl = "brave://settings/cookies";
  }

  chrome.tabs.create({
    url: tabUrl,
    index: 0,
    pinned: false,
    active: true,
  });
}

export const toggleLinkedinSidebar = (tabId) => {
  if (tabId) {
    return chrome.tabs.sendMessage(tabId, { type: LINKEDIN_SIDEBAR_TOGGLE });
  }
};

export const showToastOnActivePage = (data, senderTabId, sendResponse) => {
  setTimeout(async () => {
    const res = await chrome.tabs.sendMessage(senderTabId, {
      type: SHOW_TOAST_ON_WEBSITE,
      data,
    });
    sendResponse(res);
  }, data?.delay ?? 3000);
};

export const showEasyApplySuccessOnActivePage = (
  data,
  senderTabId,
  sendResponse
) => {
  setTimeout(async () => {
    const res = await chrome.tabs.sendMessage(senderTabId, {
      type: OPEN_EASY_APPLY_SUCCESS,
      data,
    });
    sendResponse(res);
  }, data?.delay ?? 3000);
};

export const addExtensionPopup = () => {
  chrome.action.setPopup({ popup: "assets/popup.html" });
};
