import { BASE_URL } from "./constants";
import { chromeLocalStorage } from "../utils/chromeStorage";
import { CS_UID } from "../constants/chromeStorageConstants";
import fetchAdapter from "./fetchAdapter";

const fetchIdentifierList = async () => {
  return fetchAdapter(`${BASE_URL}/connections/getIdentifierList`, {
    method: "POST",
    body: {
      uid: await chromeLocalStorage.getItemAsync(CS_UID),
    },
    headers: {
      "Content-Type": "application/json",
    },
  });
};

const fetchReferralsFromCompany = (uid, companyName, htmlSnippet) => {
  return fetchAdapter(`${BASE_URL}/connections/getPeopleForReferralInCompany`, {
    method: "POST",
    mode: "cors",
    headers: {
      accept: "*/*",
      "Content-Type": "application/json",
    },
    body: {
      uid,
      companyName,
      htmlSnippet,
    },
  });
};

export { fetchIdentifierList, fetchReferralsFromCompany };
