import * as Sentry from "@sentry/browser";
import { sendEvent } from "../analytics/analytics";
import { SCREEN_EVENTS } from "../analytics/event";

export const DISABLED_ERROR_MESSAGES = [
  "aborted a request",
  "fetch is aborted",
  "operation was aborted",
  "receiving end does not exist",
  "browser is shutting down",
  "no tab with id",
  "failed to fetch",
  "load failed", // Network issue,
  "networkerror when attempting to fetch resource", // Network issue
  "this page cannot be scripted due to an ExtensionsSettings policy",
  "frame with id 0 was removed", // current tab deleted where enjectScript was called
  "no current window",
  "user may be dragging a tab",
  "frame with id 0 is showing error page",
  "ResizeObserver loop completed with undelivered notifications",
  "ResizeObserver loop limit exceeded",
  "Cannot access contents of url",
  "Non-Error promise rejection captured with value: Timeout",
];

const initializeSentry = () => {
  // sentry initialization error if ever should not put down service worker and content script
  try {
    Sentry.init({
      dsn: "https://765fe78abf26718fa6b11ae2ad43e2c4@o4506297141559296.ingest.sentry.io/4506392550965248",
      enabled: process.env.NODE_ENV === "production",
      integrations: [],
      tracesSampleRate: 0.1,
      beforeSend: (event, hint) => {
        const error = hint.originalException;
        if (
          DISABLED_ERROR_MESSAGES.some((errorMessage) =>
            error?.message?.toLowerCase().includes(errorMessage?.toLowerCase())
          ) ||
          (error?.stack?.includes("chrome-extension://") &&
            !error?.stack?.includes(
              "chrome-extension://figlaioomchnkcajbbkfdgjgfdkfgmdg"
            )) // don't send errors from diff extensions
        ) {
          return null;
        }
        return event;
      },
    });
  } catch (err) {
    sendEvent(SCREEN_EVENTS.SENTRY_INITIALIZATION_ERROR, { err });
  }
};

export default initializeSentry;
