import { addEntryInDbAndNotifyUser, handlePartialAutoApply } from "./index";
import { uploadFileToInput } from "../utils/uploadFileToInput";
import { CS_CANDIDATE_ID } from "../constants/chromeStorageConstants";
import { chromeLocalStorage } from "../utils/chromeStorage";
import { EXTERNAL_JOB_APPLICATION_SOURCE } from "../constants/commonConstants";

export const greenHouseApplyPageRegex =
  /boards(?:\.[a-zA-Z0-9-]+)?\.greenhouse.io\/([^\/]+)\/jobs\/([a-zA-Z0-9-]+)/i;
export const greenHouseEmbededApplyPageRegex =
  /boards(?:\.[a-zA-Z0-9-]+)?\.greenhouse.io\/embed\/([a-zA-Z0-9-]+)/i;

const pushInputValue = (element, value) => {
  // Try setting the value through clipboard
  element.value = "";
  const pasteEvent = new ClipboardEvent("paste", {
    bubbles: true,
    cancelable: true,
    clipboardData: new DataTransfer(),
  });
  pasteEvent.clipboardData.setData("text/plain", value);

  element.dispatchEvent(pasteEvent);

  // Dispatch input and change events to simulate a user action
  element.dispatchEvent(new Event("input", { bubbles: true }));
  element.dispatchEvent(new Event("change", { bubbles: true }));

  if (element.value !== value) {
    if (element.type === "number" && value.startsWith("+")) {
      element.value = value.slice(1);
    } else {
      element.value = value;
    }
    // Dispatch input and change events to simulate a user action
    element.dispatchEvent(new Event("input", { bubbles: true }));
    element.dispatchEvent(new Event("change", { bubbles: true }));
  }
};

const GPTAutoFill = async (chatGPTResponse, reApply) => {
  return new Promise(async (res) => {
    const {
      autoApplyFormSelector,
      autoApplyResumeSelector,
      autoApplyFields,
      autoApplyResumeLink,
    } = chatGPTResponse;

    let form = document.querySelector(autoApplyFormSelector);
    // not a jobs application page
    if (!form) {
      form = document
        .getElementsByTagName("iframe")?.[0]
        ?.contentWindow?.document?.querySelector(autoApplyFormSelector);
      if (!form) {
        form = document.querySelector("body");
      }
    }

    const jdUrl = window.location.href?.split("#")?.[0];

    const autoFilledFields = [];
    const autoFilledFailedFields = [];

    // Add resume
    let resumeInput;
    try {
      if (autoApplyResumeSelector) {
        resumeInput = form?.querySelector(autoApplyResumeSelector);
        if (
          greenHouseApplyPageRegex.test(jdUrl) ||
          greenHouseEmbededApplyPageRegex.test(jdUrl)
        ) {
          resumeInput = document.querySelector('input[type="file"]');
        }
        if (resumeInput?.type !== "file") {
          throw new Error(
            "Resume Selector is not a file type, trying default file input"
          );
        }
      } else {
        throw new Error("No Resume Selector, trying default file input");
      }
    } catch (e) {
      resumeInput = form.querySelector('input[type="file"]');
    } finally {
      if (resumeInput && autoApplyResumeLink) {
        await uploadFileToInput(autoApplyResumeLink, resumeInput);
        autoFilledFields.push({ field: "resume" });
      }
    }

    // Fill text input fields
    autoApplyFields.forEach(({ selector, value, type, label }) => {
      try {
        switch (type) {
          case "text":
          case "textarea":
          case "email":
          case "url":
          case "tel":
          case "number":
          case "input": {
            let element;
            let elements;
            if (selector && selector.startsWith("#")) {
              try {
                element = document.querySelector(selector);
                elements = document.querySelectorAll(selector);
              } catch (e) {
                element = document.getElementById(selector.slice(1));
              }
            } else {
              element = document.querySelector(selector);
              elements = document.querySelectorAll(selector);
            }
            if (element) {
              if (elements?.length > 1) {
                elements.forEach((el) => pushInputValue(el, value));
              } else {
                pushInputValue(element, value);
              }
              autoFilledFields.push({
                field: label,
                value,
              });
            } else {
              autoFilledFailedFields.push({
                field: label,
                value,
              });
            }
            break;
          }
          case "checkbox":
          case "radio": {
            const element = document.querySelector(selector);
            if (element) {
              element.click();
              autoFilledFields.push({
                field: label,
                value,
              });
            } else {
              autoFilledFailedFields.push({
                field: label,
                value,
              });
            }
            break;
          }
          case "select": {
            const element = document.querySelector(selector);
            if (element) {
              element.value = value;
              autoFilledFields.push({
                field: label,
                value,
              });
            } else {
              autoFilledFailedFields.push({
                field: label,
                value,
              });
            }
            break;
          }
        }
      } catch (e) {
        autoFilledFailedFields.push({
          field: label,
          value,
          error: e?.message,
        });
      }
    });

    // show partial apply
    await handlePartialAutoApply({
      jdUrl,
      source: EXTERNAL_JOB_APPLICATION_SOURCE.GPT_AutoFill,
      isPartialDomain: true,
      totalFields: autoApplyFields.map((field) => ({
        ...field,
        value: null,
      })),
      totalFieldsLength: autoApplyFields?.length || 0,
      autoFilledFields,
      autoFilledFieldsLength: autoFilledFields.length,
      autoApplyFormSelector,
      autoApplyResumeSelector,
      fieldsNotFilled: autoFilledFailedFields,
      autoFilledFieldsWithEmptyValues: autoFilledFields.filter(
        ({ value }) => value?.trim?.()?.length === 0
      ),
    });
    const candidateId = await chromeLocalStorage.getItemAsync(CS_CANDIDATE_ID);
    await addEntryInDbAndNotifyUser({
      fields: autoFilledFields,
      jdUrl,
      candidateId,
      source: EXTERNAL_JOB_APPLICATION_SOURCE.GPT_AutoFill,
      reApply,
    });
    res({ autoFilledFields });
  });
};

export default GPTAutoFill;
