export function createNewUser(
  extensionUserType,
  acquisitionSource,
  tokenRefresh = false
) {
  // Redirection will be handled by previous api call
  return getMEIdentity(DO_NOT_REDIRECT_TO_LINKEDIN_ON_FAILURE)
    .then(async (result) => {
      return Promise.all([
        { userIdentity: result },
        getUserContactInfo(
          result?.miniProfile?.publicIdentifier,
          DO_NOT_REDIRECT_TO_LINKEDIN_ON_FAILURE
        ),
        getUserProfile(
          result?.miniProfile?.publicIdentifier,
          DO_NOT_REDIRECT_TO_LINKEDIN_ON_FAILURE
        ),
      ]);
    })
    .then((result) => {
      return {
        contactInfo: result[1],
        userIdentity: result[0].userIdentity,
        rawProfile: result[2],
      };
    })
    .then(async (result) => {
      const isLoggedOut = await isLoggedOutUser();

      if (
        isNullUndefinedOrEmpty(result?.contactInfo?.emailAddress) &&
        !isLoggedOut
      ) {
        return Promise.reject({ error: "Email not found" });
      }

      // added chrome local storage
      await chromeLocalStorage.setItemAsync(
        "userEmail",
        result.contactInfo.emailAddress
      );

      const installationParams = await chromeLocalStorage.getItemAsync(
        CS_INSTALLATION_PARAMS
      );

      const referrerId = installationParams?.referrerId,
        circleReferrerOrganizationId =
          installationParams?.circleReferrerOrganizationId,
        circleReferrerUserId = installationParams?.circleReferrerUserId;

      const totalConnectionCount = await getTotalLinkedinConnectionCount();

      await chromeLocalStorage.setItemAsync(
        CS_MAX_ALL_CONNECTIONS_AVAILABLE,
        totalConnectionCount
      );

      return createUser(
        result.userIdentity,
        result.contactInfo,
        result.rawProfile,
        referrerId,
        extensionUserType,
        acquisitionSource,
        result?.userIdentity?.miniProfile?.publicIdentifier,
        totalConnectionCount,
        circleReferrerOrganizationId,
        circleReferrerUserId
      ).then(async (res) => {
        await chromeLocalStorage.setItemAsync(
          CS_SELF_PUBLIC_ID,
          res.publicIdentifier
        );
        identifyUser({
          PublicIdentifier: res.publicIdentifier,
        });
        if (tokenRefresh) {
          sendEvent(SCREEN_EVENTS.TOKEN_REFRESH, {
            publicIdentifier: res.publicIdentifier,
          });
        } else {
          sendEvent(SCREEN_EVENTS.EXTENSION_INSTALLED, {
            publicIdentifier: res.publicIdentifier,
          });
        }
        return res;
      });
    });
}

export async function syncUserConnections({ uid, selfPublicId }) {
  if (
    uid &&
    selfPublicId &&
    !(await chromeLocalStorage.getItemAsync(CS_SYNC_COMPLETE)) &&
    (await chromeLocalStorage.getItemAsync(CS_SYNC_COMPLETE)) !== true
  ) {
    const result = await getUserProfile(
      await chromeLocalStorage.getItemAsync(CS_SELF_PUBLIC_ID),
      DO_NOT_REDIRECT_TO_LINKEDIN_ON_FAILURE
    );
    const { educations, companies } = parseFullProfile(result);
    await chromeLocalStorage.setItemAsync(CS_USER_EDUCATION_AND_COMPANIES, {
      educations,
      companies,
    });
    return continueLinkedInConnectionSync(educations, companies).then(
      async () => {
        const lastSearchItemIndex = await chromeLocalStorage.getItemAsync(
          CS_LAST_SEARCH_ITEM_INDEX
        );
        return {
          success: true,
          lastSearchItemIndex,
        };
      }
    );
  } else if (
    (await chromeLocalStorage?.getItemAsync(CS_SYNC_COMPLETE)) === true &&
    (await chromeLocalStorage?.getItemAsync(CS_CONNECTION_SYNC_DONE)) !== true
  ) {
    const searchList = await chromeLocalStorage.getItemAsync(
      CS_SYNC_SEARCH_LIST
    );
    if (isNullUndefinedOrEmpty(searchList)) {
      return { success: false };
    }
    return syncConnections(
      JSON.parse(searchList),
      EXTENSION_CONSTANTS.CONNECTION_TYPE_RELEVANT,
      await chromeLocalStorage.getItemAsync(CS_SELF_PUBLIC_ID)
    ).then(async (result) => {
      if (result && result?.type == "success_sync") {
        await chromeLocalStorage.setItemAsync(CS_CONNECTION_SYNC_DONE, true);
        await updateIdentifierList(result?.identifierList);
        replaceAlarm(FETCH_IDENTIFIER_LIST, {
          when: Date.now() + FETCH_IDENTIFIER_LIST_DELAY * 1000,
        });
        sendEvent(SCREEN_EVENTS.SYNC_SECTION_SYNC_CONNECTION_SUCCESSFUL, {
          status: "Successful",
        });
        const lastSearchItemIndex = await chromeLocalStorage.getItemAsync(
          CS_LAST_SEARCH_ITEM_INDEX
        );
        return { success: true, lastSearchItemIndex };
      } else {
        sendEvent(SCREEN_EVENTS.SYNC_SECTION_SYNC_CONNECTION_UNSUCCESSFUL, {
          status: "Unsuccessful",
          result,
        });
        sendCustomErrorMessageToSentry(
          SCREEN_EVENTS.SYNC_SECTION_SYNC_CONNECTION_UNSUCCESSFUL,
          result
        );
        return { success: false };
      }
    });
  }
  return Promise.resolve();
}

// If there are 2 alarms at same time run only 1
let syncAllConnectionsIsOngoing = false;

export const syncAllConnections = async () => {
  if (await getExtensionConfigForSync("DISABLE_ALL_CONNECTION_FETCH", false))
    return;
  if (await isLoggedOutUser()) {
    const last401ErrorTime = await chromeLocalStorage.getItemAsync(
      CS_LAST_401_ERROR
    );
    replaceAlarm(SYNC_ALL_CONNECTIONS, {
      when: last401ErrorTime + (await getSyncAllConnectionsDelayTime()) * 1000,
    });
    return;
  }
  const pageSize = await getExtensionConfigForSync(
    "SYNC_ALL_CONNECTIONS_PAGE_SIZE",
    40,
    true
  );

  const connectionsToFetchInOneTime = await getExtensionConfigForSync(
    "NUMBER_OF_ALL_CONNECTIONS_FETCHED_AT_A_TIME",
    120,
    true
  );

  // eslint-disable-next-line prefer-const
  let [syncComplete, maxAllConnectionsAvailable] = await Promise.all([
    chromeLocalStorage.getItemAsync(CS_SYNC_COMPLETE),
    chromeLocalStorage.getItemAsync(CS_MAX_ALL_CONNECTIONS_AVAILABLE),
  ]);

  if (
    isNullUndefinedOrEmpty(maxAllConnectionsAvailable) ||
    isNaN(maxAllConnectionsAvailable)
  ) {
    const maxItemsPresent = await getTotalLinkedinConnectionCount();
    if (maxItemsPresent && Number.parseInt(maxItemsPresent) > 0) {
      chromeLocalStorage.setItemAsync(
        CS_MAX_ALL_CONNECTIONS_AVAILABLE,
        Number.parseInt(maxItemsPresent)
      );
      maxAllConnectionsAvailable = Number.parseInt(maxItemsPresent);
    } else {
      maxAllConnectionsAvailable = 0;
    }
  }

  let lastAllConnectionsFetchIndex =
    (await chromeLocalStorage.getItemAsync(
      CS_LAST_ALL_CONNECTIONS_FETCH_INDEX
    )) ?? 0;

  let lastAllConnectionsFetchTimestamp = await chromeLocalStorage.getItemAsync(
    CS_LAST_ALL_CONNECTION_FETCH_TIMESTAMP
  );

  if (isNaN(lastAllConnectionsFetchTimestamp)) {
    lastAllConnectionsFetchTimestamp = Date.now();
    await chromeLocalStorage.setItemAsync(
      CS_LAST_ALL_CONNECTION_FETCH_TIMESTAMP,
      lastAllConnectionsFetchTimestamp
    );
  }

  let delayTime = await getSyncAllConnectionsDelayTime();
  if (isNaN(delayTime)) {
    delayTime = 1500;
  }

  const delayComplete = isDateEqualAndMoreThanXSecondsOld(
    new Date(lastAllConnectionsFetchTimestamp),
    delayTime
  );

  if (delayComplete && syncAllConnectionsIsOngoing) {
    syncAllConnectionsIsOngoing = false;
  }

  if (
    !syncAllConnectionsIsOngoing &&
    delayComplete &&
    syncComplete &&
    syncComplete === true &&
    maxAllConnectionsAvailable > lastAllConnectionsFetchIndex
  ) {
    const connectionsLeftToFetch =
      maxAllConnectionsAvailable - lastAllConnectionsFetchIndex;

    let stepSize = Math.ceil(connectionsToFetchInOneTime / pageSize);
    if (
      typeof connectionsLeftToFetch === "number" &&
      connectionsLeftToFetch > 0 &&
      connectionsLeftToFetch < connectionsToFetchInOneTime
    ) {
      stepSize = Math.ceil(connectionsLeftToFetch / pageSize);
    }
    if (stepSize > 3) {
      stepSize = 3;
    }

    syncAllConnectionsIsOngoing = true;
    await chromeLocalStorage.setItemAsync(
      CS_LAST_ALL_CONNECTION_FETCH_TIMESTAMP,
      Date.now()
    );
    for (let step = 0; step < stepSize; step++) {
      try {
        const result = await getAllLinkedInConnectionsAsync(
          lastAllConnectionsFetchIndex,
          pageSize,
          DO_NOT_REDIRECT_TO_LINKEDIN_ON_FAILURE
        );

        if (checkArraySanity(result?.included)) {
          const unparsedList = result?.included?.filter(
            ({ publicIdentifier }) => !!publicIdentifier
          );

          lastAllConnectionsFetchIndex += unparsedList.length;
          addExtraConnections(
            [result],
            DEFAULT_SOURCE,
            API_EXTENSION_CONSTANTS.CONNECTION_TYPE_ALL_CONNECTIONS,
            false,
            false,
            false,
            maxAllConnectionsAvailable,
            await chromeLocalStorage.getItemAsync(CS_SELF_PUBLIC_ID)
          )
            .then(async (res) => {
              if (checkArraySanity(res?.identifierList)) {
                const identifierList = await chromeLocalStorage.getItemAsync(
                  CS_IDENTIFIER_LIST
                );
                const parsedIdentifierList = JSON.parse(identifierList || "[]");
                const updatedList = getUniqueIdentifiers([
                  ...parsedIdentifierList,
                  ...res.identifierList,
                ]);
                await chromeLocalStorage.setItemAsync(
                  CS_IDENTIFIER_LIST,
                  JSON.stringify(updatedList)
                );
                identifyUser({
                  ConnectionsCount: maxAllConnectionsAvailable,
                  ConnectionsSynced: lastAllConnectionsFetchIndex,
                });
              }
            })
            .catch((error) => {
              if (error?.statusCode >= 400 && error?.statusCode < 500) {
                sendEvent(SCREEN_EVENTS.ALL_CONNECTIONS_FETCH_FAILED, {
                  fetchedAt: new Date(),
                  error,
                });
                if (![401, 403].includes(error?.statusCode)) {
                  sendCustomErrorMessageToSentry(
                    SCREEN_EVENTS.ALL_CONNECTIONS_FETCH_FAILED,
                    { error }
                  );
                }
              }
            });
          await chromeLocalStorage.setItemAsync(
            CS_LAST_ALL_CONNECTIONS_FETCH_INDEX,
            lastAllConnectionsFetchIndex
          );
          await updateUserConnectionsLastIndex(lastAllConnectionsFetchIndex);
          await sleep(getRandomNumberInRange(10, 15) * 1000);
        }
      } catch (err) {
        if (err?.statusCode >= 400 && err?.statusCode < 500) {
          sendEvent(SCREEN_EVENTS.ALL_CONNECTIONS_FETCH_FAILED, {
            fetchedAt: new Date(),
            error: err,
            statusCode: err?.statusCode,
          });
          if (![401, 403].includes(err?.statusCode)) {
            sendCustomErrorMessageToSentry(
              SCREEN_EVENTS.ALL_CONNECTIONS_FETCH_FAILED,
              { error: err }
            );
          }
        }
      }
    }
    syncAllConnectionsIsOngoing = false;
    replaceAlarm(SYNC_ALL_CONNECTIONS, {
      when: Date.now() + (await getSyncAllConnectionsDelayTime()) * 1000,
    });
  }
};

// returns
// true -> if we can't access linkedin apis
// false -> if we can access linkedin apis
export const isLoggedOutUser = async () => {
  const last401ErrorTime = await chromeLocalStorage.getItemAsync(
    CS_LAST_401_ERROR
  );
  return (
    last401ErrorTime &&
    !isDateEqualAndMoreThanXSecondsOld(
      new Date(last401ErrorTime),
      2 * 24 * 60 * 60
    )
  );
};

/**
 * 1. This function allows only fetching connections in range
 *    (MIN_CONNECTIONS_TO_FETCH_IN_A_BATCH, MAX_CONNECTIONS_TO_FETCH_IN_A_BATCH)
 *    in a single batch.
 * 2. After a batch ends there should be sleep time
 *    in range (MIN_BACKGROUND_SYNCING_COMPLETE_SLEEP, MAX_BACKGROUND_SYNCING_COMPLETE_SLEEP)
 *    in minutes.
 * 3. On service worker restart if a batch has yet not over but if last call made is more than
 *    random(MIN_BACKGROUND_SYNCING_SLEEP, MAX_BACKGROUND_SYNCING_SLEEP) minutes,
 *    then close previous batch and start a new one.
 */
export const shouldPerformBackgroundSyncing = async (
  identifierList,
  secondaryIdentifierList
) => {
  let [
    batchSize,
    lastBackgroundSyncCallTime,
    backgroundSyncingCurrentCount,
    batchSleepTime,
  ] = await Promise.all([
    chromeLocalStorage.getItemAsync(CS_BACKGROUND_SYNCING_BATCH_SIZE),
    chromeLocalStorage.getItemAsync(CS_LAST_BACKGROUND_SYNC_CALL_TIME),
    chromeLocalStorage.getItemAsync(CS_BACKGROUND_SYNCING_CURRENT_COUNT),
    chromeLocalStorage.getItemAsync(CS_BACKGROUND_SYNCING_BATCH_SLEEP_TIME),
  ]);

  if (
    !checkArraySanity(identifierList) &&
    !checkArraySanity(secondaryIdentifierList)
  ) {
    // nothing to sync
    return false;
  }

  if (
    !batchSize ||
    !lastBackgroundSyncCallTime ||
    isDateEqualAndMoreThanXSecondsOld(
      new Date(lastBackgroundSyncCallTime),
      batchSleepTime * 60
    )
  ) {
    // Conclude previous batch
    if (batchSize && lastBackgroundSyncCallTime) {
      batchSize = getRandomNumberInRange(
        await getExtensionConfigForSync(
          "MIN_CONNECTIONS_TO_FETCH_IN_A_BATCH",
          220,
          true
        ),
        await getExtensionConfigForSync(
          "MAX_CONNECTIONS_TO_FETCH_IN_A_BATCH",
          300,
          true
        )
      );
    } else {
      // First time batch
      batchSize = getRandomNumberInRange(260, 300);
    }

    // Create a new batch
    lastBackgroundSyncCallTime = Date.now();
    backgroundSyncingCurrentCount = 0;
    batchSleepTime = getRandomNumberInRange(
      await getExtensionConfigForSync(
        "MIN_BACKGROUND_SYNCING_SLEEP",
        120,
        true
      ),
      await getExtensionConfigForSync("MAX_BACKGROUND_SYNCING_SLEEP", 240, true)
    );

    await Promise.all([
      chromeLocalStorage.setItemAsync(
        CS_BACKGROUND_SYNCING_BATCH_SIZE,
        batchSize
      ),
      chromeLocalStorage.setItemAsync(
        CS_LAST_BACKGROUND_SYNC_CALL_TIME,
        lastBackgroundSyncCallTime
      ),
      chromeLocalStorage.setItemAsync(
        CS_BACKGROUND_SYNCING_CURRENT_COUNT,
        backgroundSyncingCurrentCount
      ),
      chromeLocalStorage.setItemAsync(
        CS_BACKGROUND_SYNCING_BATCH_SLEEP_TIME,
        batchSleepTime
      ),
    ]);
    return true;
  }

  // Need to sleep the process
  if (backgroundSyncingCurrentCount >= batchSize) {
    const batchEndedSleep = getRandomNumberInRange(
      await getExtensionConfigForSync(
        "MIN_BACKGROUND_SYNCING_COMPLETE_SLEEP",
        10,
        true
      ),
      await getExtensionConfigForSync(
        "MAX_BACKGROUND_SYNCING_COMPLETE_SLEEP",
        20,
        true
      )
    );
    await createAlarmIfNotExist(SETUP_BG_SYNC_BATCH_END, {
      when: Date.now() + batchEndedSleep * 60 * 1000,
    });
    return false;
  }

  // ongoing syncing
  return true;
};
