import * as Sentry from "@sentry/browser";
import { forEach } from "../utils/lodash";

export const setupSentryUser = (userDetails) => {
  // see sentry docs for valid properties
  Sentry.setUser(userDetails);
};

export const addCustomSentryTags = (tags) => {
  Sentry.configureScope((scope) => {
    forEach(tags, (value, key) => {
      scope.setTag(key, value);
    });
  });
};

/**
 * Sends a custom error message to Sentry.
 * @param {string} msg - The message to send as the error.
 * @param {Object} details - Additional details about the error, structured as key-value pairs.
 */
export const sendCustomErrorMessageToSentry = (msg, details) => {
  Sentry.captureMessage(msg, {
    level: "error", // Set the severity level
    extra: details,
  });
};
