import { CS_IDENTIFIER_LIST } from "../constants/chromeStorageConstants";
import { chromeLocalStorage } from "./chromeStorage";
import { checkArraySanity } from "./lodash";

export const getUniqueIdentifiers = (list) => {
  const set = new Set();
  const uniqueIdentifiers = [];
  list.forEach((person) => {
    if (typeof person === "string") {
      if (!set.has(person)) {
        uniqueIdentifiers.push(person);
        set.add(person);
      }
    } else {
      if (!set.has(person?.publicIdentifier)) {
        uniqueIdentifiers.push(person);
        set.add(person.publicIdentifier);
      }
    }
  });
  return uniqueIdentifiers;
};

export const updateIdentifierList = async (list) => {
  if (!checkArraySanity(list)) return;
  const identifierList = JSON.parse(
    (await chromeLocalStorage.getItemAsync(CS_IDENTIFIER_LIST)) || "[]"
  );
  const updatedList = getUniqueIdentifiers([...identifierList, ...list]);
  await chromeLocalStorage.setItemAsync(
    CS_IDENTIFIER_LIST,
    JSON.stringify(updatedList)
  );
};
