export const chromeLocalStorage = {
  getItemAsync: (item) =>
    !item
      ? Promise.reject({ message: "missing key name in getItemAsync" })
      : new Promise((resolve) => {
          chrome.storage?.local?.get(item.toString(), (res) => {
            resolve(res?.[item]);
          });
        }),

  setItemAsync: (key, val) =>
    !key
      ? Promise.reject({ message: "missing key name in setItemAsync" })
      : new Promise((resolve) => {
          chrome.storage?.local?.set({ [key.toString()]: val }, (res) =>
            resolve(res)
          );
        }),

  removeItemAsync: (key) =>
    !key
      ? Promise.reject({ message: "missing key name in removeItemAsync" })
      : new Promise((resolve) => {
          chrome.storage.local?.remove(key.toString(), (res) => resolve(res));
        }),

  clear: () => {
    return new Promise((resolve) => {
      chrome.storage?.local?.clear(() => resolve());
    });
  },
};
