import React, { useCallback, useEffect, useRef, useState } from "react";
import DraggableStickyOverlay from "../DraggableStickyOverlay";
import { getImageAssetUrl } from "../../utils/common";
import { EASY_APPLY_IFRAME_ID } from "../../constants/easyApply";
import { autoFillForm, getAutoApplyDomain } from "../../autofills";
import {
  AUTO_APPLY_JD,
  CANDIDATE_DATA,
  CAREER_PAGE_REFERRALS,
  CLOSE_CAREER_PAGE_REFERRALS,
  CLOSE_MISSING_INFO_SIDEBAR,
  CLOSE_RESUME_MATCHING_SIDEBAR,
  CLOSE_RESUME_TAILORED_SIDEBAR,
  EASY_APPLY_DISCLAIMER_SHOWN,
  EASY_APPLY_FAILED,
  EASY_APPLY_LOADING,
  EASY_APPLY_REAPPLY,
  EASY_APPLY_SHOW_DISCLAIMER,
  EASY_APPLY_SUCCESS,
  MANDATORY_DETAILS_FILLED,
  OPEN_CANDIDATE_DASHBOARD,
  OPEN_LINKEDIN_SIDEBAR,
  RECALCULATE_JD_MATCH_SCORE,
  RESUME_MATCH_KEYWORDS,
  SAVE_CANDIDATE_INPUT_TO_AI,
} from "../../constants/messageConstants";
import { queryStringParse } from "../../utils/browser";
import {
  AUTO_APPLY_CUSTOM_URL_QUERY_PARAM,
  AUTO_FILL_DOMAIN,
  EXTENSION_WEBSITE_DOMAIN,
  EXTENSION_WEBSITE_URL,
} from "../../constants/urls";
import {
  checkArraySanity,
  checkObjectSanity,
  isNullUndefinedOrEmpty,
} from "../../utils/lodash";
import { getAutoAppliedApplication } from "../../api/externalJobApplication";
import { fetchIncompleteMandatoryFields } from "../../utils/autoApply";
import ResumeMatch from "../ResumeMatch";
import EasyApplyButton from "../EasyApplyButton";
import Sidebar from "../Sidebar";
import { sendEvent } from "../../analytics/analytics";
import { SCREEN_EVENTS } from "../../analytics/event";
import packageJson from "../../../package.json";
import findResumeMatchWords from "../../ContentScript/utils/keywordResumeMatching";
import ReferralButton from "../ReferralButton";
import {
  getGPTResponseForAutoFill,
  extractHTMLAsText,
} from "../../ContentScript/utils/autoApplyGPTUtils";
import EasyApplySuccessSidebar from "../EasyApplySuccessSidebar";
import { chromeLocalStorage } from "../../utils/chromeStorage";
import { fetchReferralsFromCompany } from "../../api/connection";
import "./styles.css";
import "../commonStyles.css";
import { EXTERNAL_JOB_APPLICATION_SOURCE } from "../../constants/commonConstants";
import { saveAIApplyScrapedJobs } from "../../api/adhoc";
import SaveJob from "../SaveJob";
import {
  addToSavedJobs,
  checkIfJobIsSaved,
  removeFromSavedJobs,
} from "../../api/candidate";
import TailoredResumeButton from "../TailoredResumeButton";
import TailoredResumeSidebar from "../TailoredResumeSidebar";
import { getPageDataForResume } from "../TailoredResumeSidebar/utils";

const weekdayLogoSmall = getImageAssetUrl("weekday-logo-small.png");

const userShownDisclaimer = async () => {
  return chromeLocalStorage.getItemAsync(EASY_APPLY_DISCLAIMER_SHOWN);
};

const getUserPrompt = async () => {
  return await chromeLocalStorage.getItemAsync(SAVE_CANDIDATE_INPUT_TO_AI);
};

const EasyApplyWidget = ({
  autofillIframeUrl,
  easyApplyButtonDomain,
  candidateData: data,
  candidateId,
  jobDescription,
  companyName,
  jobTitle,
  uid,
  sessionId,
}) => {
  const [isEasyApplyLoading, setIsEasyApplyLoading] = useState(false);
  const [isJdApplied, setIsJdApplied] = useState(false);
  const [jDAppliedData, setJDAppliedData] = useState({});
  const [jobSaved, setJobSaved] = useState(null);
  const [isKeywordMatchSidebarOpen, setIsKeywordMatchSidebarOpen] =
    useState(false);
  const [isResumeTailoredSidebarOpen, setIsResumeTailoredSidebarOpen] =
    useState(false);
  const [isMandatoryInfoSidebarOpen, setIsMandatoryInfoSidebarOpen] =
    useState(false);
  const [resumeTailoredLoading, setResumeRailoredLoading] = useState(true);
  const [isReferralSidebarOpen, setIsReferralSidebarOpen] = useState(false);
  const [matchedKeywords, setMatchedKeywords] = useState([]);
  const [skillKeywords, setSkillKeywords] = useState([]);
  const [candidateData, setCandidateData] = useState(data);
  const [referralData, setReferralData] = useState({});
  const [easyApplySidebarOpen, setEasyApplySidebarOpen] = useState(false);
  const [sidebarLoaded, setSidebarLoaded] = useState(false);
  const [referralButtonState, setReferralButtonState] = useState({
    loading: true,
    disable: false,
    called: false,
  });
  const [easyApplyFailed, setEasyApplyFailed] = useState(false);

  const saveJob = useCallback(
    async (cb) => {
      const imageUrl = document
        .querySelector("meta[property='og:image']")
        ?.getAttribute("content");
      const jobTitle = document
        .querySelector("meta[property='og:title']")
        ?.getAttribute("content");
      await addToSavedJobs({
        encryptedPublicIdentifier: candidateId,
        uid: uid,
        source: "EXTENSION",
        jdUrl: window.location.href,
        jobRole: jobTitle || "JD Link",
        companyLogo: imageUrl,
      }).then((res) => {
        setJobSaved(res || {});
        cb();
      });
    },
    [candidateId, uid]
  );

  const resumeMatchIframeRef = useRef();
  const resumeTailoredIframeRef = useRef();
  const referralIframeRef = useRef();
  const easyApplyIframeRef = useRef();

  const width = "58px";
  const initialPosition = {
    x: `calc(100% - ${width} - 1px)`,
    y: "20%",
  };

  const handleSignin = useCallback(() => {
    chrome.runtime.sendMessage({
      message: OPEN_LINKEDIN_SIDEBAR,
    });
  }, []);

  const handleSidebarOnLoad = useCallback(() => {
    setSidebarLoaded(true);
  }, [setSidebarLoaded]);

  const sendMessageToEasyApplySidebar = (type, data, candidateInputToGPT) => {
    if (easyApplyIframeRef.current) {
      const message = {
        type,
        data,
        candidateInputToGPT,
      };
      easyApplyIframeRef.current.contentWindow.postMessage(message, "*");
    }
  };

  const showDisclaimer = useCallback((candidateInputToGPT) => {
    setEasyApplySidebarOpen(true);
    sendMessageToEasyApplySidebar(
      EASY_APPLY_SHOW_DISCLAIMER,
      null,
      candidateInputToGPT
    );
  }, []);

  const handleEasyApply = useCallback(async () => {
    if (!candidateId) {
      return handleSignin();
    }
    const candidateInputToGPT = await getUserPrompt();
    setIsEasyApplyLoading(true);
    const missingFields = await fetchIncompleteMandatoryFields();
    if (!checkArraySanity(missingFields)) {
      const disclaimer = await userShownDisclaimer();
      if (!disclaimer) {
        showDisclaimer(candidateInputToGPT);
        return;
      }
      setEasyApplySidebarOpen(true);
      sendMessageToEasyApplySidebar(
        EASY_APPLY_LOADING,
        null,
        candidateInputToGPT
      );
      const jobUrl = window.location.href;
      if (!jobUrl.includes(AUTO_APPLY_CUSTOM_URL_QUERY_PARAM.weekdayJdUid)) {
        saveAIApplyScrapedJobs({
          type: "click",
          jobUrl,
          text: extractHTMLAsText(),
        });
      }
    }
    if (checkArraySanity(missingFields)) {
      setIsMandatoryInfoSidebarOpen(true);
    } else if (autofillIframeUrl) {
      const res = await chrome.runtime.sendMessage({
        message: AUTO_APPLY_JD,
        data: {
          url: autofillIframeUrl,
          domain: getAutoApplyDomain(autofillIframeUrl),
          reApply: isJdApplied,
        },
      });
      if (res?.error) {
        sendMessageToEasyApplySidebar(
          EASY_APPLY_FAILED,
          {
            message: `${res?.message}`,
          },
          candidateInputToGPT
        );
        setEasyApplyFailed(true);
        sendEvent(SCREEN_EVENTS.AUTO_APPLY_FILL_FORM_FAILED, {
          jdUrl: window.location.href,
          autoApplyFailedReason: `${res?.message}`,
        });
      } else if (res?.success) {
        const { autoFilledFields } = res?.data;
        sendMessageToEasyApplySidebar(
          EASY_APPLY_SUCCESS,
          {
            message: `Autofill complete! Please verify the data once.`,
            autoFilledFields,
          },
          candidateInputToGPT
        );
        if (checkObjectSanity(jobSaved)) {
          removeFromSavedJobs(candidateId, jobSaved?.id);
        }
        setJobSaved(null);
        setIsJdApplied(true);
      }
      setJDAppliedData({});
    } else {
      try {
        let responseFromGPT;
        if (![AUTO_FILL_DOMAIN.ASHBYHQ.label].includes(easyApplyButtonDomain)) {
          responseFromGPT = await getGPTResponseForAutoFill();
          if (!responseFromGPT?.autoApplyFormSelector) {
            throw new Error("GPT could not find form selector");
          }
        }
        const { autoFilledFields } = await autoFillForm(
          easyApplyButtonDomain,
          responseFromGPT,
          isJdApplied
        );
        sendMessageToEasyApplySidebar(
          EASY_APPLY_SUCCESS,
          {
            message: `Autofill complete! Please verify the data once.`,
            autoFilledFields,
          },
          candidateInputToGPT
        );
        if (checkObjectSanity(jobSaved)) {
          removeFromSavedJobs(candidateId, jobSaved?.id);
        }
        setIsJdApplied(true);
        setJobSaved(null);
      } catch (e) {
        sendMessageToEasyApplySidebar(
          EASY_APPLY_FAILED,
          {
            message: `${e?.message}`,
          },
          candidateInputToGPT
        );
        setEasyApplyFailed(true);
        sendEvent(SCREEN_EVENTS.AUTO_APPLY_FILL_FORM_FAILED, {
          jdUrl: window.location.href,
          autoApplyFailedReason: `${e?.message}`,
        });
        setJDAppliedData({});
        setIsEasyApplyLoading(false);
      }
    }
    setIsEasyApplyLoading(false);
  }, [
    autofillIframeUrl,
    easyApplyButtonDomain,
    handleSignin,
    candidateId,
    showDisclaimer,
    isJdApplied,
    jobSaved,
  ]);

  const closeEasyApplySidebar = useCallback(
    ({ close, reAIApply }) => {
      if (typeof reAIApply === "boolean" && reAIApply) {
        handleEasyApply();
      } else if (close) {
        setIsEasyApplyLoading(false);
        setEasyApplySidebarOpen(false);
      } else {
        userShownDisclaimer().then((res) => {
          if (res) {
            handleEasyApply();
          } else {
            setIsEasyApplyLoading(false);
            setEasyApplySidebarOpen(false);
          }
        });
      }
    },
    [handleEasyApply]
  );

  const checkIfJobPageIsAlreadyApplied = useCallback(
    async ({ jdUrl, candidateId, jdUid }) => {
      let data;
      if (jdUid) {
        data = await getAutoAppliedApplication({
          jdUid,
          candidateId,
        });
      } else {
        data = await getAutoAppliedApplication({
          source: EXTERNAL_JOB_APPLICATION_SOURCE.GPT_AutoFill,
          jdUrl: jdUrl.split("#")[0],
          candidateId,
        });
      }

      return {
        isAlreadyApplied: checkArraySanity(data),
        data,
      };
    },
    []
  );

  const calledRef = useRef(false);

  useEffect(() => {
    if (
      !calledRef.current &&
      sidebarLoaded &&
      !isEasyApplyLoading &&
      !isJdApplied &&
      !easyApplyFailed
    ) {
      const queryParams = queryStringParse();
      const jdUid = queryParams?.[
        AUTO_APPLY_CUSTOM_URL_QUERY_PARAM.weekdayJdUid
      ]
        ? Number(queryParams[AUTO_APPLY_CUSTOM_URL_QUERY_PARAM.weekdayJdUid])
        : undefined;
      if (!isNullUndefinedOrEmpty(jdUid) && !isNaN(jdUid)) {
        handleEasyApply();
        calledRef.current = true;
      }
    }
  }, [
    sidebarLoaded,
    isJdApplied,
    isEasyApplyLoading,
    easyApplyFailed,
    handleEasyApply,
    calledRef,
  ]);

  const makeInitialApiCalls = useCallback(async () => {
    // Check if Jd is already applied
    const jdUrl = window.location.href;
    const queryParams = queryStringParse();
    if (candidateId) {
      const jdUid = queryParams?.[
        AUTO_APPLY_CUSTOM_URL_QUERY_PARAM.weekdayJdUid
      ]
        ? Number(queryParams[AUTO_APPLY_CUSTOM_URL_QUERY_PARAM.weekdayJdUid])
        : undefined;

      try {
        const [
          { isAlreadyApplied, data: appliedData },
          savedJobData,
          referralAPIData,
        ] = await Promise.all([
          checkIfJobPageIsAlreadyApplied({
            jdUrl: autofillIframeUrl || jdUrl,
            candidateId,
            jdUid,
          }),
          checkIfJobIsSaved({
            encryptedPublicIdentifier: candidateId,
            jdUrl: window.location.href,
          }),
          companyName && uid
            ? fetchReferralsFromCompany(uid, companyName?.trim())
            : Promise.resolve({}),
        ]);

        setJobSaved(savedJobData || {});
        setJDAppliedData(appliedData);
        setIsJdApplied(isAlreadyApplied);
        setReferralData(Object.values(referralAPIData || {})?.[0]);
      } catch (err) {}
    }

    // Find resume match keywords
    if (candidateData?.resumeText) {
      const { skillKeywords, matchedKeywords } = findResumeMatchWords(
        jobDescription || extractHTMLAsText(),
        candidateData.resumeText
      );

      setSkillKeywords(skillKeywords);
      setMatchedKeywords(matchedKeywords);
    }
    setIsEasyApplyLoading(false);
  }, [
    candidateId,
    checkIfJobPageIsAlreadyApplied,
    jobDescription,
    candidateData.resumeText,
    companyName,
    uid,
    autofillIframeUrl,
  ]);

  const openSidebar = useCallback(
    ({ matchedKeywords, skillKeywords, candidateData }) => {
      const message = {
        type: RESUME_MATCH_KEYWORDS,
        data: {
          matchedKeywords,
          skillKeywords,
          candidateData,
        },
      };
      sendEvent(SCREEN_EVENTS.RESUME_KEYWORD_MATCH_SIDEBAR_OPEN, {
        matchedKeywords,
        skillKeywords,
      });
      resumeMatchIframeRef.current.contentWindow.postMessage(message, "*");
      setIsKeywordMatchSidebarOpen(true);
    },
    []
  );

  const openResumeTailoredSidebar = useCallback(async () => {
    const message = {
      type: CANDIDATE_DATA,
      data: { candidateData, pageData: await getPageDataForResume() },
    };
    resumeTailoredIframeRef.current.contentWindow.postMessage(message, "*");
    setIsResumeTailoredSidebarOpen(true);
  }, [candidateData]);

  const openReferralSidebar = useCallback(async () => {
    let referralDataToIframe = referralData;
    if (isNullUndefinedOrEmpty(companyName) && !referralButtonState.disable) {
      setReferralButtonState((prevState) => ({ ...prevState, loading: true }));
      try {
        const referralAPIData = await fetchReferralsFromCompany(
          uid,
          null,
          extractHTMLAsText()
        );
        referralDataToIframe = Object.values(referralAPIData || {})?.[0];
        setReferralData(referralDataToIframe);
      } catch (e) {
      } finally {
        setReferralButtonState({ loading: false, disable: true });
      }
    }
    const message = {
      type: CAREER_PAGE_REFERRALS,
      data: {
        referralData: referralDataToIframe,
        jdLink: window.location.href,
        jobTitle,
      },
    };
    sendEvent(SCREEN_EVENTS.REFERRAL_SIDEBAR_OPENED, {
      referralData: referralDataToIframe,
    });
    referralIframeRef.current.contentWindow.postMessage(message, "*");
    setIsReferralSidebarOpen(true);
  }, [referralData, jobTitle, companyName, referralButtonState, uid]);

  const easyApplyClickHandler = useCallback(() => {
    sendEvent(SCREEN_EVENTS.AUTO_APPLY_CLICKED, {
      autofillIframeUrl,
      link: window.location.href,
    });
    handleEasyApply();
  }, [handleEasyApply, autofillIframeUrl]);

  useEffect(() => {
    setIsEasyApplyLoading(true);
    if (sidebarLoaded) {
      makeInitialApiCalls();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sidebarLoaded]);

  const reApplyOnClick = useCallback(() => {
    getUserPrompt().then((candidateInputToGPT) => {
      setEasyApplySidebarOpen(true);
      sendMessageToEasyApplySidebar(
        EASY_APPLY_REAPPLY,
        {
          appliedData: jDAppliedData,
        },
        candidateInputToGPT
      );
    });
  }, [jDAppliedData]);

  useEffect(() => {
    // Communication with iframe
    const messageListener = (event) => {
      if (event.origin !== EXTENSION_WEBSITE_DOMAIN) {
        return;
      }

      switch (event?.data?.type) {
        case CLOSE_RESUME_MATCHING_SIDEBAR: {
          setIsKeywordMatchSidebarOpen(false);
          break;
        }

        case CLOSE_RESUME_TAILORED_SIDEBAR: {
          setIsResumeTailoredSidebarOpen(false);
          break;
        }

        case MANDATORY_DETAILS_FILLED: {
          setIsMandatoryInfoSidebarOpen(false);
          handleEasyApply();
          break;
        }

        case OPEN_CANDIDATE_DASHBOARD: {
          window.location.href = `${EXTENSION_WEBSITE_DOMAIN}?updateProfile=true`;
          break;
        }

        case CLOSE_MISSING_INFO_SIDEBAR: {
          setIsMandatoryInfoSidebarOpen(false);
          break;
        }

        case CLOSE_CAREER_PAGE_REFERRALS: {
          setIsReferralSidebarOpen(false);
          break;
        }

        case RECALCULATE_JD_MATCH_SCORE: {
          const { skillKeywords, matchedKeywords } = findResumeMatchWords(
            jobDescription,
            event?.data?.data?.resumeText
          );
          setSkillKeywords(skillKeywords);
          setMatchedKeywords(matchedKeywords);
          setCandidateData((prevData) => ({
            ...prevData,
            resumeText: event?.data?.data?.resumeText,
            resumeUrl: event?.data?.data?.resumeUrl,
          }));
          openSidebar({
            matchedKeywords,
            skillKeywords,
            candidateData: {
              ...candidateData,
              resumeText: event?.data?.data?.resumeText,
              resumeUrl: event?.data?.data?.resumeUrl,
            },
          });
          break;
        }
      }
    };

    window.addEventListener("message", messageListener);

    return () => {
      window.removeEventListener("message", messageListener);
    };
  }, [candidateData, handleEasyApply, jobDescription, openSidebar]);

  return (
    <>
      <DraggableStickyOverlay
        initialPosition={initialPosition}
        width={width}
        backgroundColor="#040432"
        id={EASY_APPLY_IFRAME_ID}
      >
        {(stickySide) => (
          <div className="easy-apply-container">
            <img src={weekdayLogoSmall} alt="logo" className="logo-small" />
            <EasyApplyButton
              onClick={easyApplyClickHandler}
              isDisabled={isJdApplied}
              isLoading={isEasyApplyLoading}
              stickySide={stickySide}
              openSidebar={() => setEasyApplySidebarOpen(true)}
              reApplyOnClick={reApplyOnClick}
            />
            <TailoredResumeButton
              onClick={openResumeTailoredSidebar}
              stickySide={stickySide}
              isLoading={resumeTailoredLoading}
            />
            {isNullUndefinedOrEmpty(companyName) ||
            (!isNullUndefinedOrEmpty(companyName) &&
              checkArraySanity(referralData?.people)) ? (
              <ReferralButton
                onClick={openReferralSidebar}
                stickySide={stickySide}
                numberOfReferrals={referralData?.people?.length}
                referralButtonState={referralButtonState}
              />
            ) : null}
            {checkArraySanity(skillKeywords) ? (
              <ResumeMatch
                stickySide={stickySide}
                matchedKeywords={matchedKeywords}
                skillKeywords={skillKeywords}
                openResumeMatchSidebar={() =>
                  openSidebar({ matchedKeywords, skillKeywords, candidateData })
                }
              />
            ) : null}
            {!isNullUndefinedOrEmpty(jobSaved) ? (
              <SaveJob
                stickySide={stickySide}
                saved={jobSaved}
                onSave={saveJob}
              />
            ) : null}
          </div>
        )}
      </DraggableStickyOverlay>

      <Sidebar isOpen={isKeywordMatchSidebarOpen}>
        <iframe
          width="100%"
          height="100%"
          ref={resumeMatchIframeRef}
          src={`${EXTENSION_WEBSITE_URL}/resumeJdMatch?candidateId=${candidateId}&extensionVersion=${packageJson?.version}&skipRemoteConfig=true&sessionId=${sessionId}`}
        />
      </Sidebar>

      <TailoredResumeSidebar
        ref={resumeTailoredIframeRef}
        isSidebarOpen={isResumeTailoredSidebarOpen}
        closeSidebar={() => setIsResumeTailoredSidebarOpen(false)}
        onLoad={() => {
          setResumeRailoredLoading(false);
        }}
        candidateId={candidateId}
        sessionId={sessionId}
      />

      {isMandatoryInfoSidebarOpen ? (
        <Sidebar isOpen>
          <iframe
            width="100%"
            height="100%"
            src={`${EXTENSION_WEBSITE_URL}/autoApply/mandatoryInfo?candidateId=${candidateId}&extensionVersion=${packageJson?.version}&sessionId=${sessionId}`}
          />
        </Sidebar>
      ) : null}

      <Sidebar isOpen={isReferralSidebarOpen}>
        <iframe
          width="100%"
          height="100%"
          ref={referralIframeRef}
          onLoad={() =>
            setReferralButtonState((prevState) => ({
              ...prevState,
              loading: false,
            }))
          }
          src={`${EXTENSION_WEBSITE_URL}/careerPageReferrals?candidateId=${candidateId}&extensionVersion=${packageJson?.version}&skipRemoteConfig=true&sessionId=${sessionId}`}
        />
      </Sidebar>
      <EasyApplySuccessSidebar
        candidateId={candidateId}
        ref={easyApplyIframeRef}
        onLoad={handleSidebarOnLoad}
        isSidebarOpen={easyApplySidebarOpen}
        closeSidebar={closeEasyApplySidebar}
        sessionId={sessionId}
      />
    </>
  );
};

export default EasyApplyWidget;
