import { getCompaniesAndEducationQueryOrDefault } from "../utils/fullprofileparser";
import { syncConnections } from "../api/user";
import {
  EXTENSION_CONSTANTS,
  getSyncConnectionsPageSize,
} from "../constants/commonConstants";
import { sendEvent } from "../analytics/analytics";
import { SCREEN_EVENTS } from "../analytics/event";
import { chromeLocalStorage } from "../utils/chromeStorage";
import { getLinkedInConnectionsAsync } from "../api/linkedin";
import {
  FETCH_IDENTIFIER_LIST,
  FETCH_IDENTIFIER_LIST_DELAY,
} from "../constants/alarm";
import {
  CS_LAST_SEARCH_ITEM_INDEX,
  CS_SELF_PUBLIC_ID,
  CS_SYNC_COMPLETE,
  CS_SYNC_SEARCH_LIST,
} from "../constants/chromeStorageConstants";
import { DO_NOT_REDIRECT_TO_LINKEDIN_ON_FAILURE } from "../api/constants";
import { sendCustomErrorMessageToSentry } from "../sentry/utils";
import { getExtensionConfigForSync, sleep } from "../utils/common";
import { replaceAlarm } from "../utils/extension";
import { updateIdentifierList } from "../utils/identifierList";

export const continueLinkedInConnectionSync = async (
  educationsList,
  companiesList
) => {
  const searchComponent = `(${getCompaniesAndEducationQueryOrDefault({
    companiesList,
    educationsList,
  })})`.replace("undefined", "Google");

  const encodedQuery = encodeURIComponent(searchComponent)
    .replaceAll("(", "%28")
    .replaceAll(")", "%29");

  if (!((await chromeLocalStorage?.getItemAsync(CS_SYNC_COMPLETE)) === true)) {
    const tempLastSearchItemIndex = await chromeLocalStorage.getItemAsync(
      CS_LAST_SEARCH_ITEM_INDEX
    );
    const lastSearchItemIndex = tempLastSearchItemIndex
      ? Number.parseInt(tempLastSearchItemIndex)
      : 0;
    return callSearchApi(lastSearchItemIndex, encodedQuery);
  }
  return Promise.resolve();
};

async function callSearchApi(lastItem, query) {
  const maxSearchResults =
    (await getExtensionConfigForSync(
      "NUMBER_OF_RELEVANT_CONNECTIONS",
      80,
      true
    )) -
    (await getExtensionConfigForSync(
      "RELEVANT_CONNECTIONS_PAGE_SIZE",
      10,
      true
    ));

  const searchResList = await chromeLocalStorage.getItemAsync(
    CS_SYNC_SEARCH_LIST
  );
  const rawList = searchResList ? searchResList : [];

  let list = [];
  try {
    list = JSON.parse(rawList);
  } catch (err) {
    list = [];

    await chromeLocalStorage.removeItemAsync(CS_SYNC_SEARCH_LIST);
    await chromeLocalStorage.removeItemAsync(CS_SYNC_COMPLETE);
    await chromeLocalStorage.removeItemAsync(CS_LAST_SEARCH_ITEM_INDEX);
  }

  let maxItemsPresent = 0;

  const syncComplete = await chromeLocalStorage.getItemAsync(CS_SYNC_COMPLETE);
  if (!syncComplete && syncComplete !== true) {
    const resJson = await getLinkedInConnectionsAsync({
      searchQuery: query,
      startIndex: lastItem,
      pageSize: await getSyncConnectionsPageSize(),
      shouldRedirectToLinkedinOnFailure: DO_NOT_REDIRECT_TO_LINKEDIN_ON_FAILURE,
    });
    // sendEvent(SCREEN_EVENTS.SYNC_SECTION_SYNC_CONNECTION_ASYNC_CALL);
    maxItemsPresent =
      resJson?.data?.data?.searchDashClustersByAll?.metadata
        ?.totalResultCount || 0;
    if (maxItemsPresent && Number.parseInt(maxItemsPresent) > 0) {
      await chromeLocalStorage.setItemAsync(
        "maxConnectionsAvailable",
        maxItemsPresent
      );
    }
    if (resJson?.included) {
      const unparsedList = resJson?.included?.filter((unparsedConnection) => {
        return (
          unparsedConnection?.template === "UNIVERSAL" &&
          unparsedConnection?.trackingUrn?.includes("urn:li:member")
        );
      });

      if (
        !unparsedList ||
        unparsedList?.length < (await getSyncConnectionsPageSize()) ||
        lastItem >= maxSearchResults
      ) {
        await chromeLocalStorage.setItemAsync(CS_SYNC_COMPLETE, true);

        list.push(resJson);

        await chromeLocalStorage.setItemAsync(
          CS_SYNC_SEARCH_LIST,
          JSON.stringify(list)
        );

        await chromeLocalStorage.setItemAsync(
          CS_LAST_SEARCH_ITEM_INDEX,
          unparsedList.length + lastItem
        );

        return syncConnections(
          JSON.parse(
            await chromeLocalStorage.getItemAsync(CS_SYNC_SEARCH_LIST)
          ),
          EXTENSION_CONSTANTS.CONNECTION_TYPE_RELEVANT,
          await chromeLocalStorage.getItemAsync(CS_SELF_PUBLIC_ID)
        ).then(async (result) => {
          if (result && result?.type == "success_sync") {
            await chromeLocalStorage.setItemAsync("connectionSyncDone", true);
            await updateIdentifierList(result?.identifierList);
            await chromeLocalStorage.removeItemAsync(CS_SYNC_SEARCH_LIST);
            replaceAlarm(FETCH_IDENTIFIER_LIST, {
              when: Date.now() + FETCH_IDENTIFIER_LIST_DELAY * 1000,
            });
            sendEvent(SCREEN_EVENTS.SYNC_SECTION_SYNC_CONNECTION_SUCCESSFUL, {
              status: "Successful",
            });
          } else {
            sendEvent(SCREEN_EVENTS.SYNC_SECTION_SYNC_CONNECTION_UNSUCCESSFUL, {
              status: "Unsuccessful",
              result,
            });
            sendCustomErrorMessageToSentry(
              SCREEN_EVENTS.SYNC_SECTION_SYNC_CONNECTION_UNSUCCESSFUL,
              result
            );
          }
        });
      } else {
        list.push(resJson);

        await chromeLocalStorage.setItemAsync(
          CS_SYNC_SEARCH_LIST,
          JSON.stringify(list)
        );

        await chromeLocalStorage.setItemAsync(
          CS_LAST_SEARCH_ITEM_INDEX,
          unparsedList.length + lastItem
        );
        const lastSearchItemIndex = await chromeLocalStorage.getItemAsync(
          CS_LAST_SEARCH_ITEM_INDEX
        );

        if (
          Number.parseInt(lastSearchItemIndex) >= maxItemsPresent &&
          Number.parseInt(lastSearchItemIndex) >= maxSearchResults
        ) {
          await chromeLocalStorage.setItemAsync(CS_SYNC_COMPLETE, true);

          return syncConnections(
            JSON.parse(
              await chromeLocalStorage.getItemAsync(CS_SYNC_SEARCH_LIST)
            ),
            EXTENSION_CONSTANTS.CONNECTION_TYPE_RELEVANT,
            await chromeLocalStorage.getItemAsync(CS_SELF_PUBLIC_ID)
          ).then(async (result) => {
            if (result && result?.type == "success_sync") {
              await chromeLocalStorage.setItemAsync("connectionSyncDone", true);
              await updateIdentifierList(result?.identifierList);
              replaceAlarm(FETCH_IDENTIFIER_LIST, {
                when: Date.now() + FETCH_IDENTIFIER_LIST_DELAY * 1000,
              });
              sendEvent(SCREEN_EVENTS.SYNC_SECTION_SYNC_CONNECTION_SUCCESSFUL, {
                status: "Successful",
              });
            }
          });
        } else {
          const tempLastSearchItemIndex = await chromeLocalStorage.getItemAsync(
            CS_LAST_SEARCH_ITEM_INDEX
          );
          const lastSearchItemIndex = tempLastSearchItemIndex
            ? Number.parseInt(tempLastSearchItemIndex)
            : 0;
          await sleep(
            await getExtensionConfigForSync(
              "SYNC_RELEVANT_CONNECTIONS_SLEEP_TIME",
              500,
              true
            )
          );
          return callSearchApi(Number.parseInt(lastSearchItemIndex), query);
        }
      }
    }
  }
}
