import React from "react";
import "./styles.css";

const DotsLoader = ({ color = "#fff" }) => {
  return (
    <div className="loader">
      {[1, 2, 3].map((index) => (
        <div
          key={index}
          className="dot"
          style={{
            backgroundColor: color,
          }}
        />
      ))}
    </div>
  );
};

export default DotsLoader;
