import { BASE_URL } from "./constants";
import fetchAdapter from "./fetchAdapter";

export const addToSavedJobs = (body) => {
  return fetchAdapter(`${BASE_URL}/candidate/addToSavedJobs`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: body,
  });
};

export const checkIfJobIsSaved = (body) => {
  return fetchAdapter(`${BASE_URL}/candidate/checkIfJobIsSaved`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: body,
  });
};

export const removeFromSavedJobs = (encryptedPublicIdentifier, id, jdUid) => {
  return fetchAdapter(`${BASE_URL}/candidate/removeFromSavedJobs`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: { encryptedPublicIdentifier, id, jdUid },
  });
};
