import { getAllLinkedInConnectionsCount } from "../api/linkedin";

export const getTotalLinkedinConnectionCount = async () => {
  const linkedinData = await getAllLinkedInConnectionsCount();
  const totalConnectionCount =
    linkedinData?.data?.data?.searchDashClustersByAll?.metadata?.primaryFilterCluster?.primaryFilters?.[0]?.primaryFilterValues?.find(
      ({ value }) => value === "CONNECTIONS"
    )?.count;
  return isNaN(totalConnectionCount) ? 0 : totalConnectionCount;
};
