import React from "react";
import {
  AUTO_APPLY_JD,
  CONTENT_SCRIPT_CLEANUP,
  KEYWORD_MATCHING,
  OPEN_EASY_APPLY_SUCCESS,
  SHOW_TOAST_ON_WEBSITE,
  TOGGLE_AUTO_APPLY_SIDEBAR,
} from "../constants/messageConstants";
import {
  AUTO_APPLY_CUSTOM_URL_QUERY_PARAM,
  AUTO_FILL_DOMAIN,
} from "../constants/urls";
import { addToast } from "../utils/autoApply";
import {
  guessAutoFillTypeFromDom,
  getAutoApplyDomain,
  getAutoFillConfig,
  autoFillForm,
} from "../autofills";
import { isOpenedInIframe } from "../utils/browser";
import initializeSentry from "../sentry/initialize";
import { getParsedJobDetilsFromCurrentPage } from "./utils/easyApplyJobScrapper";
import { EASY_APPLY_IFRAME_ID } from "../constants/easyApply";
import { sleep } from "../utils/common";
import {
  attachReactComponentToDom,
  shadowDomQuerySelector,
} from "../utils/shadowDom";
import EasyApplyWidget from "../components/EasyApplyWidget";
import EasyApplySuccessSidebar from "../components/EasyApplySuccessSidebar";
import { chromeLocalStorage } from "../utils/chromeStorage";
import { CS_SESSION_ID, CS_UID } from "../constants/chromeStorageConstants";
import {
  getGPTResponseForAutoFill,
  extractHTMLAsText,
} from "./utils/autoApplyGPTUtils";
import { saveAIApplyScrapedJobs } from "../api/adhoc";
import { getFirebaseRemoteConfigObj } from "../api/internalServices";
import { CACHE_CONFIG, getCachedResponse } from "../utils/cacheApiResponse";
import { EXTENSION_CONFIG_KEY } from "../constants/commonConstants";
import { checkArraySanity } from "../utils/lodash";

initializeSentry();

const easyApplyButtonDomain =
  getAutoApplyDomain(window.location.href) || guessAutoFillTypeFromDom();

const removeEasyApplyWidget = () => {
  const widget = shadowDomQuerySelector(`#${EASY_APPLY_IFRAME_ID}`);
  if (!widget) return;
  widget?.remove();
};

const isBlacklistedPage = async () => {
  const remoteConfig = await getCachedResponse(
    getFirebaseRemoteConfigObj,
    CACHE_CONFIG.REMOTE_CONFIG
  );
  const { pattern, blacklistedUrls } =
    JSON.parse(remoteConfig?.[EXTENSION_CONFIG_KEY]?.defaultValue?.value)
      ?.autoApplyAllowedPages || {};
  const url = window?.location?.href;
  if (
    checkArraySanity(pattern) &&
    pattern.some((blacklist) => url.includes(blacklist))
  ) {
    return true;
  }
  if (
    checkArraySanity(blacklistedUrls) &&
    blacklistedUrls.some((blacklist) => url.includes(blacklist))
  ) {
    return true;
  }

  return false;
};

// let all the content load
sleep(1000).then(async () => {
  let autofillIframeUrl; // This is set for main page content script which has a autofill iframe domain present
  const pageIframes = [...document.querySelectorAll("iframe")];

  // Check if there is any iframe which we can autofill
  for (const frame of pageIframes) {
    if (
      frame.src?.startsWith("https") &&
      !AUTO_FILL_DOMAIN.EIGHTFOLD.regex.test(frame.src) // eightfold can have its own domain iframes
    ) {
      const domain = getAutoApplyDomain(frame.src);
      if (domain) {
        autofillIframeUrl = frame.src;
        break;
      }
    }
  }

  if (
    !isOpenedInIframe() &&
    !shadowDomQuerySelector(`#${EASY_APPLY_IFRAME_ID}`)
  ) {
    const { candidateData, candidateId } = await getAutoFillConfig();
    const uid = await chromeLocalStorage.getItemAsync(CS_UID);
    const sessionId = await chromeLocalStorage.getItemAsync(CS_SESSION_ID);

    // Get job details
    const jobData = await (autofillIframeUrl
      ? chrome.runtime.sendMessage({
          message: KEYWORD_MATCHING,
          data: {
            url: autofillIframeUrl,
            domain: getAutoApplyDomain(autofillIframeUrl),
          },
        })
      : getParsedJobDetilsFromCurrentPage(easyApplyButtonDomain));

    attachReactComponentToDom(
      <EasyApplyWidget
        autofillIframeUrl={autofillIframeUrl}
        easyApplyButtonDomain={easyApplyButtonDomain}
        candidateData={candidateData}
        candidateId={candidateId}
        jobDescription={jobData?.jobDescription}
        companyName={jobData?.companyName}
        uid={uid}
        jobTitle={jobData?.jobTitle}
        sessionId={sessionId}
      />
    );

    const jobUrl = window.location.href;
    if (jobUrl.includes(AUTO_APPLY_CUSTOM_URL_QUERY_PARAM.weekdayJdUid)) return;
    const isBlacklisted = await isBlacklistedPage();
    if (isBlacklisted) return;
    saveAIApplyScrapedJobs({
      type: "visit",
      jobUrl,
      text: extractHTMLAsText(),
    });
  }
});

let messageListener = null;
const messageHandler = async (message, sender, sendResponse) => {
  switch (message.type) {
    case TOGGLE_AUTO_APPLY_SIDEBAR: {
      sendResponse("done");
      break;
    }

    case SHOW_TOAST_ON_WEBSITE: {
      if (isOpenedInIframe()) return;
      addToast(message.data);
      sendResponse("done");
      break;
    }

    case OPEN_EASY_APPLY_SUCCESS: {
      if (isOpenedInIframe()) return;
      attachReactComponentToDom(<EasyApplySuccessSidebar {...message.data} />);
      sendResponse("done");
      break;
    }

    case CONTENT_SCRIPT_CLEANUP: {
      removeEasyApplyWidget();
      if (messageListener) {
        chrome.runtime.onMessage.removeListener(messageListener);
      }
      sendResponse("done");
      break;
    }

    case KEYWORD_MATCHING: {
      if (
        getAutoApplyDomain(message?.data?.url) ===
        getAutoApplyDomain(window.location.href)
      ) {
        getParsedJobDetilsFromCurrentPage(message?.data?.domain).then(
          sendResponse
        );
      }
      break;
    }

    case AUTO_APPLY_JD: {
      if (
        getAutoApplyDomain(message?.data?.url) ===
        getAutoApplyDomain(window.location.href)
      ) {
        try {
          const responseFromGPT = await getGPTResponseForAutoFill();
          const res = autoFillForm(
            message?.data?.domain,
            responseFromGPT,
            message?.data?.reApply
          );
          sendResponse({ success: true, data: res });
        } catch (e) {
          sendResponse({ error: true, message: e?.message });
        }
      }
      break;
    }
  }
};

messageListener = (...args) => {
  messageHandler(...args);
  return true; // this we need to return in case our handler calls sendResponse asynchronously
};

chrome.runtime.onMessage.addListener(messageListener);
