import { sendEvent } from "../../analytics/analytics";
import { SCREEN_EVENTS } from "../../analytics/event";
import { getAutoApplyDataForCandidate } from "../../api/autofill";
import { CS_SELF_PUBLIC_ID } from "../../constants/chromeStorageConstants";
import { SAVE_CANDIDATE_INPUT_TO_AI } from "../../constants/messageConstants";
import { chromeLocalStorage } from "../../utils/chromeStorage";

const cleanHTMLRegExpList = [
  /style="(.*?)"/gm, // remove all style="*"
  /<path[^>]*>|<\/path>/g, // remove all <path>*</path>
  /<script[^>]*>|<\/script>/g, // remove all scripts tags
  /<header[^>]*>|<\/header>/g, // remove all header tags
  /<footer[^>]*>|<\/footer>/g, // remove all footer tags
  /<style[^>]*>|<\/style>/g, // remove all style tags
  /<link[^>]*>|<\/link>/g, // remove all link tags
  /\s\s+/g, // remove tabspaces
];

const extractFieldsWithNoForm = () => {
  const parsedHTML = parseAndCloneHTML({ selector: "body" });
  if (!parsedHTML) {
    return null;
  }
  ["script", "link", "style", "iframe"].forEach((el) => {
    const els = parsedHTML.querySelectorAll(el);
    els.forEach((el) => el.remove());
  });
  return parsedHTML.cloneNode(true);
};

const parseAndCloneHTML = ({ selector, all = false }, iframe = false) => {
  let element;
  if (iframe) {
    const iframeDocument =
      document.getElementsByTagName("iframe")?.[0]?.contentWindow?.document;
    element = all
      ? iframeDocument?.querySelectorAll(selector)
      : iframeDocument?.querySelector(selector);
  } else {
    element = all
      ? document.querySelectorAll(selector)
      : document.querySelector(selector);
  }
  if (!element || (all && element?.length < 1)) {
    if (selector === "form") {
      return extractFieldsWithNoForm();
    }
    return null;
  }
  if (all) {
    const tempElement = document.createElement("div");
    element.forEach((el) => tempElement.append(el.cloneNode(true)));
    return tempElement.cloneNode(true);
  }
  return element.cloneNode(true);
};

/*
 * This function is used to get meta and title tags for checking company name
 * sanitize and remove all the script and link tags
 */
export const extractHTMLAsText = () => {
  const parsedHTML = parseAndCloneHTML({ selector: "head" });
  if (!parsedHTML) {
    return null;
  }
  ["script", "link", "style"].forEach((input) => {
    const inputs = parsedHTML.querySelectorAll(input);
    inputs.forEach((el) => el.remove());
  });
  const tempDiv = document.createElement("div");
  tempDiv.appendChild(parsedHTML);
  const bodyText = document.body.innerText;
  return `
${tempDiv.innerHTML}

${bodyText}
  `;
};

/*
 * This function is used to get the first form from the page
 * Sanitize and remove all user data
 * and convert to string to get selectors for easy apply.
 */
export const parsePageForAutoApply = () => {
  let parsedHTML = parseAndCloneHTML({ selector: "form", all: true });
  if (!parsedHTML) {
    try {
      parsedHTML = parseAndCloneHTML({ selector: "form", all: true }, true);
    } catch (e) {
      return null;
    }
    if (!parsedHTML) {
      return null;
    }
  }
  if (!parsedHTML.querySelectorAll("input")?.length) {
    return null;
  }
  const selectInputs = parsedHTML.querySelectorAll("select");
  selectInputs.forEach((select) => {
    if (select?.options?.length > 20) {
      select.remove();
    } else {
      try {
        select.value = null;
      } catch (e) {}
    }
  });
  const allInputs = parsedHTML.querySelectorAll("input");
  allInputs.forEach((input) => {
    try {
      input.value = null;
    } catch (e) {}
  });
  const tempDiv = document.createElement("div");
  tempDiv.appendChild(parsedHTML);
  const htmlString = tempDiv.innerHTML;
  let cleanedHtmlString = htmlString.replace(/[\t\r\n]+/g, "");
  cleanHTMLRegExpList.forEach((regExp) => {
    cleanedHtmlString = cleanedHtmlString.replace(regExp, "");
  });
  return cleanedHtmlString;
};

/*
 * This function attempts to send the html form layout to AI
 * to try to identify eligible and compatible fields which can be filled using the extension.
 */
export const getGPTResponseForAutoFill = async () => {
  const htmlStrForm = parsePageForAutoApply();
  if (!htmlStrForm) {
    throw new Error(
      "Could not AI apply. Please go to the application form page and click on “AI apply”"
    );
  }
  try {
    const publicIdentifier = await chromeLocalStorage.getItemAsync(
      CS_SELF_PUBLIC_ID
    );
    sendEvent(SCREEN_EVENTS.AI_APPLY_TRIGGERED);
    const userPrompt = await chromeLocalStorage.getItemAsync(
      SAVE_CANDIDATE_INPUT_TO_AI
    );
    const { autoApplyData } = await getAutoApplyDataForCandidate(
      htmlStrForm,
      publicIdentifier,
      userPrompt
    );
    if (!autoApplyData)
      throw new Error(
        "Could not AI apply. Please go to the application form page and click on “AI apply”"
      );
    return autoApplyData;
  } catch (e) {
    throw new Error(e?.message);
  }
};
