import { CS_PIC_URL } from "../constants/chromeStorageConstants";
import { SEARCH_CONNECTIONS_QUERY_ID } from "../constants/linkedinApiQueryId";
import { CACHE_CONFIG, getCachedResponse } from "../utils/cacheApiResponse";
import { chromeLocalStorage } from "../utils/chromeStorage";
import { linkedFetchWrapper } from "./util";

/**
 * This API is being used to retrieve profile picture, name and heading of a user which has
 * the keyword typed by the user. This is being used to vote for a linkedin connection for a
 * Poll question generated for user.
 */
const getUnfilteredLinkedInConnections = async (keyword, count) => {
  return linkedFetchWrapper({
    fetchArgs: [
      `https://www.linkedin.com/voyager/api/relationships/dash/connections?decorationId=com.linkedin.voyager.dash.deco.web.mynetwork.ConnectionList-5&count=${count}&keyword=${keyword}&q=search&start=0`,
      {
        headers: {
          accept: "application/vnd.linkedin.normalized+json+2.1",
          "accept-language": "en-GB,en-US;q=0.9,en;q=0.8,fi;q=0.7,hi;q=0.6",
          "csrf-token": await chromeLocalStorage.getItemAsync("JSESSION"),
          "sec-fetch-mode": "cors",
          "sec-fetch-site": "same-origin",
        },
        referrerPolicy: "strict-origin-when-cross-origin",
        method: "GET",
        mode: "cors",
        credentials: "include",
      },
    ],
  }).then((e) => {
    return e.json();
  });
};

/**
 * This api is used to get 1st degree connections of user. We use this information to help users
 * to vouch their connections for referral or ask referral from them.
 */
const getLinkedInConnectionsAsync = async ({
  searchQuery,
  startIndex,
  shouldRedirectToLinkedinOnFailure,
}) => {
  const response = await linkedFetchWrapper({
    shouldRedirectToLinkedinOnFailure,
    fetchArgs: [
      // if page size is not passed it take count equal to default set by linkedin
      `https://www.linkedin.com/voyager/api/graphql?variables=(start:${startIndex},origin:FACETED_SEARCH,query:(keywords:${searchQuery},flagshipSearchIntent:SEARCH_SRP,queryParameters:List((key:network,value:List(F)),(key:resultType,value:List(PEOPLE))),includeFiltersInResponse:false))&queryId=${SEARCH_CONNECTIONS_QUERY_ID}`,
      {
        headers: {
          accept: "application/vnd.linkedin.normalized+json+2.1",
          "accept-language": "en-GB,en;q=0.9,hi;q=0.8,en-US;q=0.7,pa;q=0.6",
          "csrf-token": await chromeLocalStorage.getItemAsync("JSESSION"),
          "sec-fetch-mode": "cors",
          "sec-fetch-site": "same-origin",
          "x-restli-protocol-version": "2.0.0",
        },
        referrerPolicy: "strict-origin-when-cross-origin",
        body: null,
        method: "GET",
        mode: "cors",
        credentials: "include",
      },
    ],
  });
  const res = await response.json();
  return res;
};

