/**
 * This api is being used to get current and past companies and schools. This
 * information helps us to suggest relevant jobs to user. It also helps to receive feedback
 * of their connections
 */
const getUserProfile = async (uid, shouldRedirectToLinkedinOnFailure) => {
  return linkedFetchWrapper({
    shouldRedirectToLinkedinOnFailure,
    fetchArgs: [
      `https://www.linkedin.com/voyager/api/identity/dash/profiles?q=memberIdentity&memberIdentity=${uid}&decorationId=com.linkedin.voyager.dash.deco.identity.profile.FullProfileWithEntities-89`,
      {
        headers: {
          "user-agent":
            "Mozilla/5.0 (X11; Linux x86_64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/86.0.4240.80 Safari/537.36",
          accept: "*/*",
          "accept-language": "ru-RU,ru;q=0.9,en-US;q=0.8,en;q=0.7,uk;q=0.6",
          "csrf-token": await chromeLocalStorage.getItemAsync("JSESSION"),
          "x-requested-with": "XMLHttpRequest",
        },
        credentials: "same-origin",
      },
    ],
  }).then((result) => {
    if (result.ok) return result.json();
    return Promise.reject(result);
  });
};

