import { sendEvent } from "../analytics/analytics";
import { SCREEN_EVENTS } from "../analytics/event";
import { getIdentifierByUid } from "../api/user";
import { CS_SESSION_ID, CS_UID } from "../constants/chromeStorageConstants";
import { EXTENSION_USER_TYPE } from "../constants/commonConstants";
import { REFRESH_SESSION_TOKEN } from "../constants/messageConstants";
import { chromeLocalStorage } from "../utils/chromeStorage";
import { createNewUser, isLoggedOutUser } from "./sync";

let windows;
chrome?.windows?.getCurrent(function (win) {
  windows = win;
});

if (!windows && typeof window !== "undefined") {
  windows = window;
}

let refreshTokenInProcess = false;
let disableUntilNextLogin = false;

export const disableRefreshTokenRefresh = (disable = true) => {
  disableUntilNextLogin = disable;
  if (!disable) {
    refreshTokenInProcess = false;
  }
};

export const refreshSession = async () => {
  const isLoggedOut = await isLoggedOutUser();
  if (isLoggedOut) {
    return null;
  }
  return new Promise((resolve, reject) => {
    createNewUser(EXTENSION_USER_TYPE.CANDIDATE, null, true)
      .then((result) => {
        if (result?.auth?.sessionToken) {
          chromeLocalStorage
            .setItemAsync(CS_SESSION_ID, result?.auth?.sessionToken)
            .then(() => resolve(result?.auth?.sessionToken));
        } else {
          reject(`Something went wrong: ${JSON.stringify(result)}`);
        }
      })
      .catch((err) => {
        disableRefreshTokenRefresh(true);
        sendEvent(SCREEN_EVENTS.TOKEN_REFRESH_ERROR, {
          message: err?.message,
          error: JSON.stringify(err),
        });
        resolve(null);
      });
  });
};

export const getOrRefreshToken = async () => {
  const uid = await chromeLocalStorage.getItemAsync(CS_UID);
  await getIdentifierByUid(uid);
  const token = await chromeLocalStorage.getItemAsync(CS_SESSION_ID);
  return token;
};

export const isTokenRefreshAllowed = () => {
  return !refreshTokenInProcess && !disableUntilNextLogin;
};

export const handleSessionTokenRefresh = async () => {
  let token = null;
  const currentLocation = windows?.location?.href;
  if (
    currentLocation &&
    !currentLocation.includes("linkedin.com") &&
    isTokenRefreshAllowed()
  ) {
    refreshTokenInProcess = true;
    token = await chrome.runtime.sendMessage({
      message: REFRESH_SESSION_TOKEN,
    });
    refreshTokenInProcess = false;
    return token;
  }

  if (isTokenRefreshAllowed()) {
    refreshTokenInProcess = true;
    token = await refreshSession();
    refreshTokenInProcess = false;
  }
  return token;
};
