import React, { forwardRef, useEffect, useMemo, useCallback } from "react";
import Sidebar from "../Sidebar";
import {
  EXTENSION_WEBSITE_DOMAIN,
  EXTENSION_WEBSITE_URL,
} from "../../constants/urls";
import {
  CLOSE_EASY_APPLY_DISCLAIMER,
  CLOSE_EASY_APPLY_SUCCESS,
  EASY_APPLY_AGREE_DISCLAIMER,
  EASY_APPLY_DISCLAIMER_SHOWN,
  HANDLE_RE_AI_APPLY,
  OPEN_APPLIED_JOBS,
  OPEN_CANDIDATE_DASHBOARD,
} from "../../constants/messageConstants";
import { CS_CANDIDATE_ID } from "../../constants/chromeStorageConstants";
import packageJson from "../../../package.json";
import { chromeLocalStorage } from "../../utils/chromeStorage";
import { sendEvent } from "../../analytics/analytics";
import { SCREEN_EVENTS } from "../../analytics/event";

const EasyApplySuccessSidebar = forwardRef(
  (
    {
      candidateId,
      isPartialApply,
      closeSidebar,
      isSidebarOpen,
      onLoad,
      sessionId,
    },
    ref
  ) => {
    const iframeSrc = useMemo(() => {
      const url = new URL(`${EXTENSION_WEBSITE_URL}/easyApplySuccess`);
      url.searchParams.append("skipRemoteConfig", true);
      url.searchParams.append("sessionId", sessionId);
      url.searchParams.append(CS_CANDIDATE_ID, candidateId);
      url.searchParams.append("extensionVersion", packageJson?.version);
      if (isPartialApply) {
        url.searchParams.append("isPartialApply", "true");
      }
      return url.toString();
    }, [isPartialApply, candidateId, sessionId]);

    const easyApplyDisclaimerAgree = useCallback(async () => {
      await chromeLocalStorage.setItemAsync(EASY_APPLY_DISCLAIMER_SHOWN, true);
      sendEvent(SCREEN_EVENTS.AUTO_APPLY_DISCLAIMER_CLICKED);
      closeSidebar({ close: false });
    }, [closeSidebar]);

    useEffect(() => {
      // Communication with iframe
      const messageListener = (event) => {
        if (event.origin !== EXTENSION_WEBSITE_DOMAIN) {
          return;
        }

        switch (event?.data?.type) {
          case CLOSE_EASY_APPLY_DISCLAIMER: {
            sendEvent(SCREEN_EVENTS.AUTO_APPLY_DISCLAIMER_DISSMISSED);
            closeSidebar({ close: true });
            break;
          }

          case HANDLE_RE_AI_APPLY: {
            closeSidebar({ close: false, reAIApply: true });
            break;
          }

          case CLOSE_EASY_APPLY_SUCCESS: {
            closeSidebar({ close: true });
            break;
          }

          case OPEN_CANDIDATE_DASHBOARD: {
            window.location.href = `${EXTENSION_WEBSITE_DOMAIN}?updateProfile=true`;
            break;
          }

          case OPEN_APPLIED_JOBS: {
            window.location.href = `${EXTENSION_WEBSITE_DOMAIN}/applied`;
            break;
          }

          case EASY_APPLY_AGREE_DISCLAIMER: {
            easyApplyDisclaimerAgree();
            break;
          }
        }
      };

      window.addEventListener("message", messageListener);

      return () => {
        window.removeEventListener("message", messageListener);
      };
    }, [closeSidebar, easyApplyDisclaimerAgree]);

    return (
      <Sidebar isOpen={isSidebarOpen}>
        <iframe
          onLoad={onLoad}
          width="100%"
          height="100%"
          src={iframeSrc}
          ref={ref}
          id="auto-apply-sidebar"
        />
      </Sidebar>
    );
  }
);

EasyApplySuccessSidebar.displayName = "EasyApplySuccessSidebar";

export default EasyApplySuccessSidebar;
