import React, { useCallback, useMemo, useState } from "react";
import { STICKY_SIDE } from "../../constants/commonConstants";
import "./styles.css";
import DotsLoader from "../Loaders/DotsLoader";
import { getImageAssetUrl } from "../../utils/common";

const tailorResumeImage = getImageAssetUrl("tailor-resume.png");

const TailoredResumeButton = ({ onClick, stickySide, isLoading }) => {
  const [isHovered, setIsHovered] = useState(false);

  const handleMouseEnter = useCallback(() => {
    setIsHovered(true);
  }, []);

  const handleMouseLeave = useCallback(() => {
    setIsHovered(false);
  }, []);

  const handleClick = useCallback(
    (e) => {
      if (isLoading) return;
      const target = e.currentTarget;
      target.classList.add("active");
      setTimeout(() => target.classList.remove("active"), 300);
      onClick();
    },
    [onClick, isLoading]
  );

  const textContent = useMemo(
    () => <p className="easy-apply--text">AI Tailored Resume</p>,
    []
  );

  return (
    <div
      className="widget-button"
      style={
        isHovered
          ? {
              transform: `translateX(${
                stickySide === STICKY_SIDE.LEFT ? "70px" : "-70px"
              })`,
            }
          : {}
      }
    >
      <div
        className={`widget-button-content ${
          isHovered ? "widget-button-content-slide" : ""
        } cursor-pointer`}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        onClick={handleClick}
      >
        {isHovered && stickySide === STICKY_SIDE.LEFT ? textContent : null}
        {isLoading ? (
          <DotsLoader color="rgb(55, 5, 46)" />
        ) : (
          <img src={tailorResumeImage} alt="Tailor Resume" width="40" />
        )}
        {isHovered && stickySide === STICKY_SIDE.RIGHT ? textContent : null}
      </div>
    </div>
  );
};

export default TailoredResumeButton;
