const set = (object, keys, val) => {
  keys = Array.isArray(keys) ? keys : keys.split(".");
  if (keys.length > 1) {
    object[keys[0]] = object[keys[0]] || {};
    return set(object[keys[0]], keys.slice(1), val);
  }
  object[keys[0]] = val;
};

const isObject = (value) => {
  const type = typeof value;
  return value != null && (type === "object" || type === "function");
};

const mapArray = (array, iteratee) => {
  let index = -1;
  const length = array == null ? 0 : array.length;
  const result = new Array(length);

  while (++index < length) {
    result[index] = iteratee(array[index], index, array);
  }
  return result;
};

const mapObject = (object, iteratee) => {
  const props = Object.keys(object);
  const result = new Array(props.length);

  props.forEach((key, index) => {
    result[index] = iteratee(object[key], key, object);
  });
  return result;
};

const map = (value, iteratee) => {
  return isObject(value)
    ? mapObject(value, iteratee)
    : mapArray(value, iteratee);
};

const debounce = (cb, delay = 1000) => {
  let timeout;
  return (...args) => {
    clearTimeout(timeout);
    timeout = setTimeout(() => {
      cb(...args);
    }, delay);
  };
};

const throttle = (cb, delay = 1000) => {
  let shouldWait = false;
  let waitingArgs;
  const timeoutFunc = () => {
    if (waitingArgs == null) {
      shouldWait = false;
    } else {
      cb(...waitingArgs);
      waitingArgs = null;
      setTimeout(timeoutFunc, delay);
    }
  };

  return (...args) => {
    if (shouldWait) {
      waitingArgs = args;
      return;
    }

    cb(...args);
    shouldWait = true;
    setTimeout(timeoutFunc, delay);
  };
};

const deepCopy = (inObject) => {
  let value, key;

  if (typeof inObject !== "object" || inObject === null) {
    return inObject; // Return the value if inObject is not an object
  }

  // Create an array or object to hold the values
  const outObject = Array.isArray(inObject) ? [] : {};

  for (key in inObject) {
    value = inObject[key];

    // Recursively (deep) copy for nested objects, including arrays
    outObject[key] = deepCopy(value);
  }

  return outObject;
};

const checkObjectSanity = (object) => {
  if (
    typeof object !== "object" ||
    Array.isArray(object) ||
    object === null ||
    Object.keys(object).length == 0
  )
    return false;
  return true;
};

const checkArraySanity = (list) => {
  if (list && Array.isArray(list) && list?.length > 0) {
    return true;
  }

  return false;
};

const isNullUndefinedOrEmpty = (data) => {
  return (
    data === null ||
    data === undefined ||
    Number.isNaN(data) ||
    data === Infinity ||
    (typeof data === "string" && data?.trim() == "")
  );
};

const forEachArray = (array, iteratee) => {
  const length = array.length;
  let index = -1;

  while (++index < length) {
    iteratee(array[index], index, array);
  }
};

const forEachObject = (object, iteratee) => {
  const keys = Object.keys(object);

  keys.forEach((key) => {
    iteratee(object[key], key, object);
  });
};

const forEach = (value, iteratee) => {
  if (typeof value !== "object") return;
  isObject(value)
    ? forEachObject(value, iteratee)
    : forEachArray(value, iteratee);
};

export {
  set,
  debounce,
  throttle,
  isObject,
  map,
  mapArray,
  mapObject,
  deepCopy,
  checkObjectSanity,
  checkArraySanity,
  isNullUndefinedOrEmpty,
  forEach,
};
