import { EXTENSION_CONSTANTS } from "../constants/commonConstants";
import { chromeLocalStorage } from "../utils/chromeStorage";
import { BASE_URL } from "./constants";
import {
  CS_EXTENSION_LOCATION,
  CS_UID,
} from "../constants/chromeStorageConstants";
import fetchAdapter from "./fetchAdapter";

const createUser = async (
  userIdentity,
  contactInfo,
  rawProfile,
  referrerId,
  extensionUserType,
  acquisitionSource,
  publicIdentifier,
  userReportedConnectionCount,
  circleReferrerOrganizationId,
  circleReferrerUserId
) => {
  return fetchAdapter(
    `${BASE_URL}/user/setupAcc`,
    {
      headers: {
        accept: "*/*",
        "accept-language": "en-GB,en-US;q=0.9,en;q=0.8",
        "content-type": "application/json",
      },
      referrerPolicy: "strict-origin-when-cross-origin",
      method: "POST",
      mode: "cors",
      credentials: "omit",
      body: {
        userIdentity,
        contactInfo,
        groupType: EXTENSION_CONSTANTS.CURRENT_GROUP,
        extensionLocation: await chromeLocalStorage.getItemAsync(
          CS_EXTENSION_LOCATION
        ),
        rawProfile,
        referrerId,
        scoutExtensionUserType: extensionUserType,
        acquisitionSource,
        publicIdentifier,
        userReportedConnectionCount,
        circleReferrerOrganizationId,
        circleReferrerUserId,
      },
    },
    60
  );
};

const syncConnections = async (
  connectionData,
  connectionType,
  publicIdentifier
) => {
  return fetchAdapter(`${BASE_URL}/user/syncConnections`, {
    headers: {
      accept: "*/*",
      "accept-language": "en-GB,en-US;q=0.9,en;q=0.8",
      "content-type": "application/json",
      "sec-ch-ua":
        '"Chromium";v="86", "\\"Not\\\\A;Brand";v="99", "Google Chrome";v="86"',
      "sec-ch-ua-mobile": "?0",
      "sec-fetch-dest": "empty",
      "sec-fetch-mode": "cors",
      "sec-fetch-site": "none",
    },
    referrerPolicy: "strict-origin-when-cross-origin",
    method: "POST",
    mode: "cors",
    credentials: "omit",
    body: {
      connectionData,
      uid: await chromeLocalStorage.getItemAsync(CS_UID),
      groupType: EXTENSION_CONSTANTS.CURRENT_GROUP,
      connectionType,
      publicIdentifier,
    },
  });
};

const addExtraConnections = async (
  connectionList,
  source,
  connectionType,
  returnScored,
  enableFiltering,
  shouldUpdateConnectionType,
  userReportedConnectionCount,
  publicIdentifier
) => {
  return fetchAdapter(`${BASE_URL}/user/addConnections`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: {
      connectionList,
      uid: await chromeLocalStorage.getItemAsync(CS_UID),
      groupType: EXTENSION_CONSTANTS.CURRENT_GROUP,
      source: source,
      connectionType,
      returnScored,
      enableFiltering,
      shouldUpdateConnectionType,
      userReportedConnectionCount,
      publicIdentifier,
    },
  });
};

const getUserDetailsFromId = async (uid) => {
  return fetchAdapter(`${BASE_URL}/user/getUserDetailsFromId`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: {
      uid: uid || (await chromeLocalStorage.getItemAsync(CS_UID)),
    },
  });
};

const getUserLocation = async (candidateProfile) => {
  return fetchAdapter(
    `${BASE_URL}/candidateProfiles/fetchCountryNameFromCandidateProfileData`,
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: {
        candidateProfile,
      },
    }
  );
};

const fetchCandidateByIdentifier = (encryptedPublicIdentifier) => {
  const url = `${BASE_URL}/candidateProfiles/fetchCandidateByIdentifier`;
  return fetchAdapter(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: {
      encryptedPublicIdentifier,
      requireAdditionalInfo: true,
    },
  });
};

const getIdentifierByUid = (uid) => {
  return fetchAdapter(`${BASE_URL}/user/getIdentifierByUid`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: {
      uid,
    },
  });
};

const updateUserConnectionsLastIndex = async (connectionsLastIndex) => {
  return fetchAdapter(`${BASE_URL}/user/updateUserConnectionsLastIndex`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: {
      connectionsLastIndex,
      uid: await chromeLocalStorage.getItemAsync(CS_UID),
    },
  });
};

export {
  createUser,
  syncConnections,
  addExtraConnections,
  getUserDetailsFromId,
  getUserLocation,
  fetchCandidateByIdentifier,
  getIdentifierByUid,
  updateUserConnectionsLastIndex,
};
