import { getAutoFillConfig } from "../../autofills";
import {
  ashybqApplyPageRegex,
  ashybqDescriptionPageRegex,
} from "../../autofills/ashbyhqAutoFill";
import { AUTO_FILL_DOMAIN } from "../../constants/urls";
import {
  eightfoldPageRegex,
  freshteamApplyPageRegex,
  greenHouseApplyPageRegex,
  jobviteApplyPageRegex,
  jobviteDescriptionPageRegex,
  kekaDescriptionPageRegex,
  leverApplyPageRegex,
  leverDescriptionPageRegex,
  pyjamahrPageRegex,
  ripplingApplyPageRegex,
  ripplingDescriptionPageRegex,
  saasbuddyPageRegex,
  smartrecruitersApplyPageRegex,
  smartrecruitersDescriptionPageRegex,
  zohorecruitPageRegex,
} from "../../utils/AIApplyRegex";
import { queryStringParse } from "../../utils/browser";
import { checkArraySanity } from "../../utils/lodash";

const getGreenhouseJobDetails = (remoteConfig) => {
  const jobUrl = window.location.href;

  if (!greenHouseApplyPageRegex.test(jobUrl)) return null;

  const jobTitle = document.querySelector(remoteConfig?.jobTitle)?.innerText;
  const companyName = document
    .querySelector(remoteConfig?.companyName)
    ?.innerText?.replace("at ", "");
  const aboutCompany = document.querySelector(
    remoteConfig?.aboutCompany
  )?.innerText;
  const jobLocation = document.querySelector(
    remoteConfig?.jobLocation
  )?.innerText;
  const jobDescription = document
    .querySelector(remoteConfig?.jobDescription)
    ?.innerText?.replace(aboutCompany || "", "")
    ?.trim();
  return {
    jobTitle,
    companyName,
    aboutCompany,
    jobLocation,
    jobUrl,
    jobDescription,
  };
};

const getLeverJobDetails = (remoteConfig) => {
  const jobUrl = window.location.href;
  const isDetailsPage =
    leverDescriptionPageRegex.test(jobUrl) && !leverApplyPageRegex.test(jobUrl);

  // Not a job description page just return
  if (!isDetailsPage) return null;

  const jobTitle = document.querySelector(remoteConfig?.jobTitle)?.innerText;
  const companyName = jobUrl.match(leverDescriptionPageRegex)?.[1];
  const companyLogoUrl = document.querySelector(
    remoteConfig?.companyLogoUrl
  )?.src;
  const jobLocation = document.querySelector(
    remoteConfig?.jobLocation
  )?.innerText;
  const jobDescription = document
    .querySelector(remoteConfig?.jobDescription)
    ?.innerText?.replace("\nAPPLY FOR THIS JOB", "")
    ?.trim();
  const remotePolicy = document
    .querySelector(remoteConfig?.remotePolicy)
    ?.innerText?.replace("/", "")
    ?.trim();
  const jobType = document
    .querySelector(remoteConfig?.jobType)
    ?.innerText?.replace("/", "")
    ?.trim();

  return {
    jobTitle,
    companyName,
    jobLocation,
    jobUrl,
    jobDescription,
    companyLogoUrl,
    remotePolicy,
    jobType,
  };
};

const getAshbyhqJobDetails = (remoteConfig) => {
  const jobUrl = window.location.href;
  const isDetailsPage =
    ashybqDescriptionPageRegex.test(jobUrl) &&
    !ashybqApplyPageRegex.test(jobUrl);

  // Not a job description page just return
  if (!isDetailsPage) return null;

  const sidebarInfoDbMapping = remoteConfig?.sidebarInfoDbMapping;
  const jobTitle = document.querySelector(remoteConfig?.jobTitle)?.innerText;
  const companyName = jobUrl.match(ashybqDescriptionPageRegex)?.[1];
  const jobDescription = document.querySelector(
    remoteConfig?.jobDescription
  )?.innerText;
  const companyLogoUrl = document.querySelector(
    remoteConfig?.companyLogoUrl
  )?.src;
  const keyPoints = [...document.querySelectorAll(remoteConfig?.keyPoints)].map(
    (keyword) => keyword.children
  );

  const extraData = {};
  if (checkArraySanity(keyPoints)) {
    for (const [heading, text] of keyPoints) {
      const dbKey = sidebarInfoDbMapping[heading?.innerText.toLowerCase()];
      if (dbKey) {
        extraData[dbKey] = text?.innerText;
      }
    }
  }
  return {
    jobTitle,
    companyName,
    jobUrl,
    jobDescription,
    companyLogoUrl,
    ...extraData,
  };
};

const getFreshteamJobDetails = (remoteConfig) => {
  const jobUrl = window.location.href;

  if (!freshteamApplyPageRegex.test(jobUrl)) return null;

  const jobTitle = document.querySelector(remoteConfig?.jobTitle)?.innerText;
  const companyName = jobUrl.match(freshteamApplyPageRegex)?.[1];
  const companyLogoUrl = document.querySelector(
    remoteConfig?.companyLogoUrl
  )?.src;
  const jobTypeContent = document.querySelector(
    remoteConfig?.jobType
  )?.innerText;
  const jobLocation = document
    .querySelector(remoteConfig?.jobLocation)
    ?.innerText?.replace(jobTypeContent || "", "")
    ?.trim();
  const jobDescription = document.querySelector(
    remoteConfig?.jobDescription
  )?.innerText;
  const jobType = jobTypeContent
    .toLowerCase()
    ?.includes(remoteConfig?.jobTypeReplaceText)
    ? jobTypeContent
        .toLowerCase()
        ?.replace(remoteConfig?.jobTypeReplaceText, "")
        ?.trim()
    : undefined;

  return {
    jobTitle,
    companyName,
    jobLocation,
    jobUrl,
    jobDescription,
    companyLogoUrl,
    jobType,
  };
};

const getJobviteJobDetails = (remoteConfig) => {
  const jobUrl = window.location.href;

  const isDetailsPage =
    jobviteDescriptionPageRegex.test(jobUrl) &&
    !jobviteApplyPageRegex.test(jobUrl);

  // Not a job description page just return
  if (!isDetailsPage) return null;

  const jobTitle = document.querySelector(remoteConfig?.jobTitle)?.innerText;
  const companyName = jobUrl.match(jobviteDescriptionPageRegex)?.[1];
  const companyLogoUrl = document
    .querySelector(remoteConfig?.companyLogoUrl)
    ?.getAttribute(remoteConfig?.companyLogoUrlAttribute);
  const jobLocationText = document.querySelector(remoteConfig?.jobLocation);
  const jobLocation =
    jobLocationText.childNodes[
      jobLocationText.childNodes.length - 1
    ].textContent.trim();
  const jobDescription = document.querySelector(
    remoteConfig?.jobDescription
  )?.innerText;

  return {
    jobTitle,
    companyName,
    jobLocation,
    jobUrl,
    jobDescription,
    companyLogoUrl,
  };
};

