const getPyjamahrJobDetails = (remoteConfig) => {
  const jobUrl = window.location.href;
  const queryParams = queryStringParse();

  // Not a job description page just return
  if (!pyjamahrPageRegex.test(jobUrl) || !queryParams?.company) return null;

  const jobTitle = document.querySelector(remoteConfig?.jobTitle)?.innerText;
  const companyName = queryParams?.company;
  const companyLogoUrl = document.querySelector(
    remoteConfig?.companyLogoUrl
  )?.src;
  const jobLocation = document.querySelector(
    remoteConfig?.jobLocation
  )?.innerText;
  const jobDescription = document.querySelector(
    remoteConfig?.jobDescription
  )?.innerText;
  const remotePolicy = document.querySelector(
    remoteConfig?.remotePolicy
  )?.innerText;
  const jobType = document.querySelector(remoteConfig?.jobType)?.innerText;
  const salaryRange = document.querySelector(
    remoteConfig?.salaryRange
  )?.innerText;
  const experienceReqd = document.querySelector(
    remoteConfig?.experienceReqd
  )?.innerText;
  const skillTags = [
    ...document.querySelectorAll(remoteConfig?.skillTags),
  ]?.map((node) => node?.innerText);

  return {
    jobTitle,
    companyName,
    jobLocation,
    jobUrl,
    jobDescription,
    companyLogoUrl,
    remotePolicy,
    jobType,
    salaryRange,
    experienceReqd,
    skillTags,
  };
};

const getZohorecruitJobDetails = (remoteConfig) => {
  const jobUrl = window.location.href;

  // Not a job description page just return
  if (!zohorecruitPageRegex.test(jobUrl)) return null;

  const sidebarInfoDbMapping = remoteConfig?.sidebarInfoDbMapping;

  const jobTitle = document.querySelector(remoteConfig?.jobTitle)?.innerText;
  const [companyName, jobType] =
    document.querySelector(remoteConfig?.companyName)?.innerText?.split("|") ||
    [];
  const companyLogoUrl = document.querySelector(
    remoteConfig?.companyLogoUrl
  )?.src;
  const [jobLocation, jobPostedTimeText] =
    document.querySelector(remoteConfig?.jobLocation)?.innerText?.split("|") ||
    [];
  const jobPostedTime = jobPostedTimeText
    ?.trim()
    ?.replace(remoteConfig?.jobPostTimeReplaceText, "");
  const jobDescription = document
    .querySelector(remoteConfig?.jobDescription)
    ?.innerText?.replace(remoteConfig?.jobDescriptionReplaceText, "");
  const keyPoints = [...document.querySelectorAll(remoteConfig?.keyPoints)].map(
    (keyword) => keyword.children
  );

  const extraData = {};
  if (checkArraySanity(keyPoints)) {
    for (const [heading, text] of keyPoints) {
      const dbKey = sidebarInfoDbMapping[heading?.innerText.toLowerCase()];
      if (dbKey) {
        extraData[dbKey] = text?.innerText;
      }
    }
  }

  return {
    jobTitle,
    companyName,
    jobLocation,
    jobUrl,
    jobDescription,
    companyLogoUrl,
    jobPostedTime,
    jobType,
    ...extraData,
  };
};

