export const getParsedJobDetilsFromCurrentPage = async (type) => {
  const { remoteConfig: autoApplyParserRemoteConfig } =
    await getAutoFillConfig();

  switch (type) {
    case AUTO_FILL_DOMAIN.GREENHOUSE.label: {
      return getGreenhouseJobDetails(
        autoApplyParserRemoteConfig[AUTO_FILL_DOMAIN.GREENHOUSE.label]
      );
    }
    case AUTO_FILL_DOMAIN.LEVER.label: {
      return getLeverJobDetails(
        autoApplyParserRemoteConfig[AUTO_FILL_DOMAIN.LEVER.label]
      );
    }
    case AUTO_FILL_DOMAIN.ASHBYHQ.label: {
      return getAshbyhqJobDetails(
        autoApplyParserRemoteConfig[AUTO_FILL_DOMAIN.ASHBYHQ.label]
      );
    }
    case AUTO_FILL_DOMAIN.FRESHTEAM.label: {
      return getFreshteamJobDetails(
        autoApplyParserRemoteConfig[AUTO_FILL_DOMAIN.FRESHTEAM.label]
      );
    }
    case AUTO_FILL_DOMAIN.JOBVITE.label: {
      return getJobviteJobDetails(
        autoApplyParserRemoteConfig[AUTO_FILL_DOMAIN.JOBVITE.label]
      );
    }
    case AUTO_FILL_DOMAIN.KEKA.label: {
      return getKekaJobDetails(
        autoApplyParserRemoteConfig[AUTO_FILL_DOMAIN.KEKA.label]
      );
    }
    case AUTO_FILL_DOMAIN.PYJAMAHR.label: {
      return getPyjamahrJobDetails(
        autoApplyParserRemoteConfig[AUTO_FILL_DOMAIN.PYJAMAHR.label]
      );
    }
    case AUTO_FILL_DOMAIN.ZOHORECRUIT.label: {
      return getZohorecruitJobDetails(
        autoApplyParserRemoteConfig[AUTO_FILL_DOMAIN.ZOHORECRUIT.label]
      );
    }
    case AUTO_FILL_DOMAIN.SMARTRECRUITERS.label: {
      return getSmartrecruitersJobDetails(
        autoApplyParserRemoteConfig[AUTO_FILL_DOMAIN.SMARTRECRUITERS.label]
      );
    }
    case AUTO_FILL_DOMAIN.RIPPLING.label: {
      return getRipplingJobDetails(
        autoApplyParserRemoteConfig[AUTO_FILL_DOMAIN.RIPPLING.label]
      );
    }
    case AUTO_FILL_DOMAIN.EIGHTFOLD.label: {
      return getEightfoldJobDetails(
        autoApplyParserRemoteConfig[AUTO_FILL_DOMAIN.EIGHTFOLD.label]
      );
    }
    case AUTO_FILL_DOMAIN.PHENOM.label: {
      return getPhenomJobDetails(
        autoApplyParserRemoteConfig[AUTO_FILL_DOMAIN.PHENOM.label]
      );
    }
    case AUTO_FILL_DOMAIN.SAASBUDDY.label: {
      return getSaasbuddyJobDetails(
        autoApplyParserRemoteConfig[AUTO_FILL_DOMAIN.SAASBUDDY.label]
      );
    }
  }
};
