export const nodesWithText = (selector, text) => {
  const elements = [...document.querySelectorAll(selector)];
  return elements.filter((element) => RegExp(text).test(element.textContent));
};

const getTransitionEndEventName = () => {
  const transitions = {
    transition: "transitionend",
    OTransition: "oTransitionEnd",
    MozTransition: "transitionend",
    WebkitTransition: "webkitTransitionEnd",
  };
  const bodyStyle = document.body.style;
  // eslint-disable-next-line no-restricted-syntax
  for (const transition in transitions) {
    if (bodyStyle[transition] !== undefined) {
      return transitions[transition];
    }
  }

  return null;
};

export const waitForTransitionEnd = (frame) => {
  return new Promise((resolve) => {
    const transitionEndEventName = getTransitionEndEventName();

    const transitionEndListener = () => {
      frame.removeEventListener(transitionEndEventName, transitionEndListener);
      resolve(true);
    };

    frame.addEventListener(transitionEndEventName, transitionEndListener);
  });
};

export const removeQueryParamFromUrl = (url, paramToRemove = []) => {
  const urlObj = new URL(url);
  const searchParams = urlObj.searchParams;

  paramToRemove.forEach((param) => {
    searchParams.delete(param);
  });

  if (searchParams.toString() === "") {
    urlObj.search = "";
  }

  return urlObj.toString();
};

export const addQueryParamToUrl = (url, paramKey, paramValue) => {
  const urlObj = new URL(url);
  const searchParams = urlObj.searchParams;

  // Add or update the query parameter
  searchParams.set(paramKey, paramValue);

  return urlObj.toString();
};

export const queryStringParse = (queryString = window.location.search) => {
  if (!queryString) {
    return {};
  }
  const query = queryString.substring(1);
  const params = query.split("&");
  const paramsObject = {};
  let pair;
  for (let i = 0; i < params.length; i++) {
    pair = params[i].split("=");
    paramsObject[pair[0]] = decodeURIComponent(pair[1]);
  }
  return paramsObject;
};

export const queryStringStringify = (paramsObject) => {
  if (!paramsObject) {
    return "";
  }
  return Object.keys(paramsObject)
    .map((key) => {
      return (
        encodeURIComponent(key) + "=" + encodeURIComponent(paramsObject[key])
      );
    })
    .join("&");
};

export const isOpenedInIframe = () => {
  try {
    return window.self !== window.top;
  } catch (e) {
    return true;
  }
};
