import skillKeywords from "../../assets/skillKeywords.json";
import stopWords from "../../assets/stopwords.json";

const unimportantSymbolsToReplace = /[\.,\n\//(/)\\]/g;
const sampleStopWord = " i ";

const stopWordsPattern = new RegExp(
  `\\b(?<!-)(?:${stopWords.join("|")}|\\.|,)(?!-)\\b`,
  "i"
);

// Full word to word matching
const isKeywordPresentInSentence = (sentence, keyword) => {
  if (!sentence?.length || !keyword?.length) return false;

  const startIndex = sentence?.toLowerCase()?.indexOf(keyword?.toLowerCase());
  const endIndex = startIndex + keyword?.length;

  return (
    (startIndex === 0 || sentence[startIndex - 1] === " ") &&
    (endIndex === sentence.length || sentence[endIndex] === " ")
  );
};

const removeStopWordsFromParagraph = (paragraph) => {
  const jdPhrases = paragraph
    ?.replaceAll(unimportantSymbolsToReplace, sampleStopWord)
    ?.split(stopWordsPattern)
    ?.map((sentence) => sentence.trim())
    ?.filter((sentence) => sentence.length > 0);

  return jdPhrases.join(" . ");
};

const findResumeMatchWords = (jdText, resumeText) => {
  const skillKeywordsFound = [];
  const jdTextWithoutStopWords = removeStopWordsFromParagraph(jdText);
  skillKeywords.forEach((keywords) => {
    for (const keyword of keywords) {
      if (isKeywordPresentInSentence(jdTextWithoutStopWords, keyword)) {
        skillKeywordsFound.push({ allWords: keywords, foundWord: keyword });
        return;
      }
    }
  });

  const resumeWithoutStopWords = removeStopWordsFromParagraph(resumeText);
  const skillKeywordsMatch = [];
  skillKeywordsFound.forEach((skill) => {
    for (const keyword of skill.allWords) {
      if (isKeywordPresentInSentence(resumeWithoutStopWords, keyword)) {
        skillKeywordsMatch.push(skill.foundWord);
        return;
      }
    }
  });

  return {
    skillKeywords: skillKeywordsFound.map((x) => x.foundWord),
    matchedKeywords: skillKeywordsMatch,
  };
};

export default findResumeMatchWords;
