import React, { useCallback, useMemo, useState } from "react";
import Percentage from "../Percentage";
import { STICKY_SIDE } from "../../constants/commonConstants";
import "./styles.css";

const THRESHOLD_PERCENTAGE = 60;

const ResumeMatch = ({
  stickySide,
  matchedKeywords,
  skillKeywords,
  openResumeMatchSidebar,
}) => {
  const [isHovered, setIsHovered] = useState(false);

  const handleMouseEnter = useCallback(() => {
    setIsHovered(true);
  }, []);

  const handleMouseLeave = useCallback(() => {
    setIsHovered(false);
  }, []);

  const handleClick = useCallback(
    (e) => {
      const target = e.currentTarget;
      target.classList.add("active");
      setTimeout(() => target.classList.remove("active"), 300);
      setIsHovered(false);
      openResumeMatchSidebar();
    },
    [openResumeMatchSidebar]
  );

  const textContent = useMemo(
    () => (
      <p className="resume-match--text">
        {matchedKeywords.length} keywords match
      </p>
    ),
    [matchedKeywords.length]
  );

  const percentageResumeMatch = Math.round(
    (matchedKeywords?.length / skillKeywords?.length) * 100
  );

  const percentageColor =
    percentageResumeMatch >= THRESHOLD_PERCENTAGE ? "#186750" : "#f21616";

  return (
    <div
      className="resume-match-wrapper"
      style={
        isHovered
          ? {
              transform: `translateX(${
                stickySide === STICKY_SIDE.LEFT ? "65px" : "-65px"
              })`,
            }
          : {}
      }
    >
      <div
        className={`resume-match ${isHovered ? "resume-match-slide" : ""}`}
        onMouseEnter={handleMouseEnter}
        onMouseLeave={handleMouseLeave}
        onClick={handleClick}
      >
        {isHovered && stickySide === STICKY_SIDE.LEFT ? textContent : null}
        <div className="resume-match-percentage">
          <Percentage
            percentage={percentageResumeMatch}
            radius={20}
            mainColor={percentageColor}
            textColor="#000"
          />
        </div>
        {isHovered && stickySide === STICKY_SIDE.RIGHT ? textContent : null}
      </div>
    </div>
  );
};

export default ResumeMatch;
