import { chromeLocalStorage } from "../utils/chromeStorage";
import { checkObjectSanity } from "../utils/lodash";
import {
  CS_EXTENSION_LOCATION,
  CS_EXTENSION_USER_TYPE,
  CS_SELF_PUBLIC_ID,
  CS_UID,
} from "../constants/chromeStorageConstants";
import { EXTENSION_USER_TYPE } from "../constants/commonConstants";
import { SHOULD_SEND_MIXPANEL_EVENTS } from "../api/constants";
import { sendAnalyticsEvent, analyticsIdentifyUser } from "../api/analytics";
import { getCurrentTime } from "../utils/dateTime";

const DISTINCT_ID = `${Date.now()}${Math.floor(Math.random() * 100000)}`;

let windows;
chrome?.windows?.getCurrent(function (win) {
  windows = win;
});

if (!windows && typeof window !== "undefined") {
  windows = window;
}

const identifyUser = async (properties) => {
  if (!SHOULD_SEND_MIXPANEL_EVENTS) return;
  const userIdentifier =
    (await chromeLocalStorage.getItemAsync(CS_SELF_PUBLIC_ID)) || DISTINCT_ID;

  // API timeout should not stop service worker
  try {
    analyticsIdentifyUser({
      project: "JOBS_PLATFORM",
      distinctId: userIdentifier || DISTINCT_ID,
      userProperties: {
        $name: await chromeLocalStorage.getItemAsync("liUserName"),
        $email: await chromeLocalStorage.getItemAsync("userEmail"),
        uid: await chromeLocalStorage.getItemAsync(CS_UID),
        tenClicks: await chromeLocalStorage.getItemAsync("tenClicks"),
        ...properties,
      },
    });
  } catch (err) {}
};

function getMergedEventData(eventData) {
  const defaultData = {};
  return { ...defaultData, ...eventData };
}

function getBrowserNameAndVersion() {
  const { userAgent } = navigator;
  const data = {
    browser: null,
    version: null,
  };
  if (userAgent.includes("Firefox/")) {
    // Firefox
    data.browser = "Firefox";
    data.version = userAgent.split("Firefox/")[1]?.split(" ")[0];
  } else if (navigator.brave?.isBrave()) {
    // Brave
    data.browser = "Brave";
    data.version = userAgent.split("Chrome/")[1]?.split(" ")[0];
  } else if (userAgent.includes("Edg/")) {
    // Edge (Chromium)
    data.browser = "Edge";
    data.version = userAgent.split("Edg/")[1]?.split(" ")[0];
  } else if (userAgent.includes("Chrome/")) {
    // Chrome
    data.browser = "Chrome";
    data.version = userAgent.split("Chrome/")[1]?.split(" ")[0];
  } else if (userAgent.includes("Safari/")) {
    // Safari
    data.browser = "Safari";
    data.version = userAgent.split("Safari/")[1]?.split(" ")[0];
  }

  return data;
}

async function getEventUploadParams(eventData) {
  const mergedEventData = getMergedEventData(eventData);
  const browserData = getBrowserNameAndVersion();
  const installationParams = await chromeLocalStorage.getItemAsync(
    "installationParams"
  );

  let eventUploadParams = {
    userName: await chromeLocalStorage.getItemAsync("userName"),
    userEmail: await chromeLocalStorage.getItemAsync("userEmail"),
    deviceTypeByScreenSize: windows?.width
      ? windows?.width > 700
        ? "Desktop"
        : "Phone"
      : "Unknown",
    location: await chromeLocalStorage.getItemAsync(CS_EXTENSION_LOCATION),
    tenClicks: await chromeLocalStorage.getItemAsync("tenClicks"),
    browserType: browserData.browser,
    browserVersion: browserData.version,
    extensionUserType:
      (await chromeLocalStorage.getItemAsync(CS_EXTENSION_USER_TYPE)) ||
      EXTENSION_USER_TYPE.CANDIDATE,
    triggeredAtTime: getCurrentTime(),
    ...mergedEventData,
  };
  if (checkObjectSanity(installationParams)) {
    eventUploadParams = { ...eventUploadParams, ...installationParams };
  }
  return eventUploadParams;
}

const sendEvent = async (eventName, eventData = {}, user) => {
  if (!SHOULD_SEND_MIXPANEL_EVENTS) return;
  const userIdentifier =
    user ??
    ((await chromeLocalStorage.getItemAsync(CS_SELF_PUBLIC_ID)) || DISTINCT_ID);
  const eventProps = await getEventUploadParams(eventData);

  // API timeout should not stop service worker
  try {
    sendAnalyticsEvent({
      eventName,
      eventProps,
      project: "JOBS_PLATFORM",
      distinctId: userIdentifier,
    });
  } catch (err) {}
};

export { sendEvent, identifyUser, getBrowserNameAndVersion };
