const parseFullProfile = (rawProfile) => {
  const educations = [];
  const positions = [];
  if (rawProfile) {
    const rawCompanies =
      rawProfile?.elements[0]?.profilePositionGroups.elements;
    const rawEducations = rawProfile?.elements[0]?.profileEducations.elements;
    for (let i = 0; i < rawCompanies.length; i++) {
      positions.push(getParsedCompany(rawCompanies[i]));
    }

    for (let j = 0; j < rawEducations.length; j++) {
      educations.push(getParsedEducation(rawEducations[j]));
    }
  }

  return {
    firstName: rawProfile.firstName,
    lastName: rawProfile.lastName,
    location: rawProfile.geoLocation?.geo?.defaultLocalizedName,
    educations: sortDescendingByTime(educations),
    companies: sortDescendingByTime(positions),
  };
};

function getParsedCompany(rawCompany) {
  let startMonth;
  let endMonth;
  let startYear;
  let endYear;
  if (rawCompany.dateRange) {
    if (rawCompany.dateRange.start) {
      startMonth = rawCompany.dateRange.start.month;
      startYear = rawCompany.dateRange.start.year;
    }
    if (rawCompany.dateRange.end) {
      endMonth = rawCompany.dateRange.end.month;
      endYear = rawCompany.dateRange.end.year;
    }
  }
  const companyName = rawCompany.companyName;
  const position = rawCompany.title;
  const description = rawCompany.description;
  const locationName = rawCompany.locationName || rawCompany.geoLocationName;
  return {
    startMonth,
    endMonth,
    startYear,
    endYear,
    companyName,
    position,
    description,
    locationName,
  };
}

function getParsedEducation(rawEducation) {
  const degreeName = rawEducation.degreeName;
  const schoolName = rawEducation.schoolName;
  let startYear;
  let endYear;
  const fieldOfStudy = rawEducation.fieldOfStudy;

  if (rawEducation.dateRange) {
    if (rawEducation.dateRange.start) {
      startYear = rawEducation.dateRange.start.year;
    }
    if (rawEducation.dateRange.end) {
      endYear = rawEducation.dateRange.end.year;
    }
  }

  return { degreeName, schoolName, startYear, endYear, fieldOfStudy };
}

function sortDescendingByTime(list) {
  if (list) {
    for (let i = 0; i < list.length; i++) {
      list[i].monthAdder = list[i].startMonth ? list[i].startMonth / 12 : 0;
      list[i].sortScore = list[i].startYear
        ? list[i].startYear + list[i].monthAdder
        : 0;
    }

    list.sort(function (a, b) {
      return b.sortScore - a.sortScore;
    });

    return list;
  }
}

const getCompaniesAndEducationQueryOrDefault = ({
  companiesList,
  educationsList,
  maxLength = 6,
}) => {
  const firstTwoCompanies = companiesList?.slice(0, 2);
  const firstTwoEducations = educationsList?.slice(0, 2);
  const defaultCompaniesList = [
    { companyName: "Google" },
    { companyName: "Microsoft" },
    { companyName: "Facebook" },
  ];
  const defaultEducationList = [
    { schoolName: "IIT" },
    { schoolName: "NIT" },
    { schoolName: "IIIT" },
  ];
  let query = "";
  const list = [
    ...firstTwoCompanies,
    ...firstTwoEducations,
    ...companiesList?.slice(2),
    ...educationsList?.slice(2),
    ...defaultCompaniesList,
    ...defaultEducationList,
  ];
  for (let i = 0; i < maxLength; i++) {
    const string = (list[i]?.companyName || list[i].schoolName)
      ?.toLowerCase()
      ?.replaceAll(" and ", " ")
      ?.replaceAll(" or ", " ");
    if (i != 0) {
      query += ` OR ${string}`;
    } else {
      query += `${string}`;
    }
  }
  return query;
};

export { parseFullProfile, getCompaniesAndEducationQueryOrDefault };
