import React, { useCallback, useEffect, useRef, useState } from "react";
import { STICKY_SIDE } from "../../constants/commonConstants";
import "./styles.css";

const DraggerSVG = ({ width, height }) => {
  const numRows = 2;
  const numCols = 3;
  const circleRadius = height / 10;
  const circles = [];

  for (let row = 0; row < numRows; row++) {
    for (let col = 0; col < numCols; col++) {
      const cx = ((col + 1) * width) / (numCols + 1);
      const cy = ((row + 1) * height) / (numRows + 1);
      circles.push(
        <circle
          key={`${row}-${col}`}
          cx={cx}
          cy={cy}
          r={circleRadius}
          fill="#929291"
        />
      );
    }
  }

  return (
    <svg
      width={width}
      height={height}
      viewBox={`0 0 ${width} ${height}`}
      xmlns="http://www.w3.org/2000/svg"
    >
      {circles}
    </svg>
  );
};

const DraggableStickyOverlay = ({
  children,
  initialPosition,
  width,
  backgroundColor,
  ...restProps
}) => {
  const [isDragging, setIsDragging] = useState(false);
  const [offset, setOffset] = useState({
    x: 0,
    y: 0,
  });
  const [position, setPosition] = useState(initialPosition);
  const [stickySide, setStickySide] = useState(STICKY_SIDE.RIGHT);
  const [isDraggerVisible, setIsDraggerVisible] = useState(false);
  const containerRef = useRef();

  const handleMouseDown = useCallback((e) => {
    setIsDragging(true);
    // Calculate the offset from the mouse pointer to the top-left corner of the widget
    setOffset({
      x: e.clientX - containerRef.current.getBoundingClientRect().left,
      y: e.clientY - containerRef.current.getBoundingClientRect().top,
    });
  }, []);

  const handleMouseMove = useCallback(
    (e) => {
      if (isDragging) {
        // Calculate the new position of the widget based on the mouse pointer position
        const newX = e.clientX - offset.x;
        const newY = e.clientY - offset.y;

        // Constrain the widget's position within the viewport
        setPosition({
          x: `${Math.max(
            0,
            Math.min(newX, window.innerWidth - containerRef.current.offsetWidth)
          )}px`,
          y: `${Math.max(
            0,
            Math.min(
              newY,
              window.innerHeight - containerRef.current.offsetHeight
            )
          )}px`,
        });
      }
    },
    [offset, isDragging]
  );

  const handleMouseUp = useCallback(() => {
    if (isDragging) {
      setIsDragging(false);

      // Calculate the center of the widget
      const widgetCenterX =
        containerRef.current.offsetLeft + containerRef.current.offsetWidth / 2;

      // Determine the side (left or right) based on the center of the widget
      const side =
        widgetCenterX < window.innerWidth / 2
          ? STICKY_SIDE.LEFT
          : STICKY_SIDE.RIGHT;
      setStickySide(side);
      // Stick the widget to the nearest side
      setPosition((prevPosition) => ({
        ...prevPosition,
        x: side === STICKY_SIDE.LEFT ? "1px" : `calc(100% - ${width} - 1px)`,
      }));
    }
  }, [isDragging, width]);

  const showDragger = useCallback(() => {
    setIsDraggerVisible(true);
  }, []);

  const hideDragger = useCallback(() => {
    setIsDraggerVisible(false);
  }, []);

  // Since react events are not bubbled above shadow-dom to document so making the shadow-root as size of whole page
  useEffect(() => {
    const shadowRoot = document.getElementById("weekday-host");
    if (isDragging) {
      shadowRoot.style.position = "absolute";
      shadowRoot.style.inset = 0;
    } else {
      shadowRoot.style.position = "static";
      shadowRoot.style.inset = "unset";
    }
  }, [isDragging]);

  useEffect(() => {
    document.addEventListener("mouseup", handleMouseUp);
    document.addEventListener("mousemove", handleMouseMove);

    return () => {
      document.removeEventListener("mouseup", handleMouseUp);
      document.removeEventListener("mousemove", handleMouseMove);
    };
  }, [handleMouseUp, handleMouseMove]);

  return (
    <div
      ref={containerRef}
      className="weekday-sticky-overlay"
      style={{
        left: position.x,
        top: position.y,
        width: width ?? "auto",
      }}
      onMouseEnter={showDragger}
      onMouseLeave={hideDragger}
      {...restProps}
    >
      <div
        style={{
          backgroundColor,
        }}
        className="weekday-sticky-overlay__content"
      >
        {children(stickySide)}
      </div>

      <div
        className={`weekday-sticky-overlay__dragger ${
          isDraggerVisible ? "active" : ""
        }`}
        onMouseDown={handleMouseDown}
      >
        <DraggerSVG width={30} height={20} />
      </div>
    </div>
  );
};

export default DraggableStickyOverlay;
