export const createAlarmIfNotExist = async (name, data) => {
  const alarm = await chrome.alarms.get(name);
  if (!alarm) {
    chrome.alarms.create(name, data);
  }
  return !!alarm;
};

export const replaceAlarm = async (name, data) => {
  await chrome.alarms.clear(name);

  chrome.alarms.create(name, data);
};
