import { autoApplyUtilityMethods } from "../ContentScript/utils/autoApplyUtilityMethods";
import {
  addEntryInDbAndNotifyUser,
  getAutoFillConfig,
  handlePartialAutoApply,
  resumeUploadFinished,
  showInvalidPageErrorMsg,
} from "./index";
import { uploadFileToInput } from "../utils/uploadFileToInput";
import { EXTERNAL_JOB_APPLICATION_SOURCE } from "../constants/commonConstants";

export const ashybqDescriptionPageRegex =
  /jobs.ashbyhq.com\/([a-zA-Z0-9-]+)\/([a-zA-Z0-9-]+)/i;
export const ashybqApplyPageRegex =
  /jobs.ashbyhq.com\/([a-zA-Z0-9-]+)\/([a-zA-Z0-9-]+)\/application/i;

const ashbyhqAutoFill = async () => {
  return new Promise(async (res) => {
    const { candidateData, remoteConfig, candidateId } =
      await getAutoFillConfig();

    const ashbyhqAutoFillConfig = remoteConfig.ashbyhq;

    let form = document.querySelector(ashbyhqAutoFillConfig?.formSelector);
    // not a ashbyhq application page
    if (!form) {
      const match = window.location.href?.match(ashybqDescriptionPageRegex);
      const jobId = match ? match[2] : null;
      const applicationPageTab = document.querySelector(
        ashbyhqAutoFillConfig.applicationPageLinkSelector
      );
      const isJobDetailsPage =
        jobId &&
        applicationPageTab?.href &&
        ashybqApplyPageRegex.test(applicationPageTab?.href);
      if (isJobDetailsPage) {
        applicationPageTab?.click();
        form = document.querySelector(ashbyhqAutoFillConfig?.formSelector);
      } else {
        showInvalidPageErrorMsg();
        return res();
      }
    }

    const autoFilledFields = [];

    // Add resume
    const resumeInput =
      form?.querySelector(ashbyhqAutoFillConfig?.resumeInputSelector) ||
      form?.querySelector(ashbyhqAutoFillConfig?.resumeInputSelector2);
    if (resumeInput && candidateData?.[ashbyhqAutoFillConfig.resumeDbKey]) {
      await uploadFileToInput(
        candidateData?.[ashbyhqAutoFillConfig.resumeDbKey],
        resumeInput
      );
      await resumeUploadFinished(() => {
        const nodes = [
          ...form?.querySelectorAll(
            ashbyhqAutoFillConfig?.resumeUploadPendingSelector
          ),
        ];
        const pendingNode = nodes.find(
          (node) => !!node.querySelector("[role='progressbar']")
        );
        return pendingNode?.dataset?.state === "hidden";
      });
      autoFilledFields.push({ field: "resume" });
    }

    const jdUrl = window.location.href?.split("#")?.[0];
    const questions = [
      ...form?.querySelectorAll(ashbyhqAutoFillConfig?.questionsSelector),
    ];
    const inputs = [];
    questions?.forEach((question) => {
      const input = question?.querySelector(
        ashbyhqAutoFillConfig?.inputSelector
      );
      if (input) {
        inputs.push({
          el: input,
          label: question?.querySelector(ashbyhqAutoFillConfig?.labelSelector),
        });
      }
    });

    const inputDbKeyMapping = ashbyhqAutoFillConfig?.inputs;

    const visibleInputs = inputs.filter(({ el }) => el?.type !== "hidden");

    // Fill text input fields
    visibleInputs?.forEach(({ el: input, label }) => {
      inputDbKeyMapping?.forEach(({ propertyName, value, dbkey, evaluate }) => {
        if (input?.getAttribute(propertyName) === value) {
          input.focus();
          if (evaluate) {
            const parameters = dbkey?.map((key) => candidateData[key]);
            const newValue = autoApplyUtilityMethods?.[evaluate]?.(
              ...parameters
            );
            if (newValue) {
              input.value = "";
              document.execCommand("insertText", false, newValue);
              input.value = newValue;
            }
          } else {
            if (candidateData?.[dbkey?.[0]]) {
              input.value = "";
              document.execCommand(
                "insertText",
                false,
                candidateData?.[dbkey?.[0]]
              );
            }
          }
          input.blur();
          autoFilledFields.push({
            field: (label?.innerText || input?.name)?.toString(),
            value: candidateData?.[dbkey?.[0]],
          });
        }
      });
    });

    await handlePartialAutoApply({
      totalFields: visibleInputs?.map(({ el: input }) => input?.name),
      autoFilledFields,
      totalFieldsLength: visibleInputs?.length,
      totalFilledFields: autoFilledFields?.length - 1,
      jdUrl,
      source: EXTERNAL_JOB_APPLICATION_SOURCE.GPT_AutoFill,
    });

    const match = jdUrl.match(ashybqApplyPageRegex);
    const jobId = match ? match[2] : undefined;

    await addEntryInDbAndNotifyUser({
      fields: autoFilledFields,
      jdUrl,
      candidateId,
      jobId,
      source: EXTERNAL_JOB_APPLICATION_SOURCE.GPT_AutoFill,
    });
    res({ autoFilledFields });
  });
};

export default ashbyhqAutoFill;
