import { EXTERNAL_JOB_APPLICATION_SOURCE } from "../constants/commonConstants";
import { BASE_URL } from "./constants";
import fetchAdapter from "./fetchAdapter";

const subpath = "externalJobApplication";

export const createJDApplication = ({
  imageUrl,
  jobTitle,
  jdUrl,
  candidateId,
  jdUid,
  linkedInJobId,
  jobId,
  source = "other",
}) => {
  const url = `${BASE_URL}/${subpath}/createApplication`;

  const jobStatus =
    source === EXTERNAL_JOB_APPLICATION_SOURCE.GPT_AutoFill
      ? EXTERNAL_JOB_APPLICATION_SOURCE.PARTIAL_APPLICATION
      : undefined;

  return fetchAdapter(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: {
      imageUrl,
      jobTitle,
      jdUrl,
      encryptedPublicIdentifier: candidateId,
      jdUid,
      linkedInJobId,
      jobId,
      source,
      jobStatus,
    },
  });
};

export const updateAutoAppliedApplicationStatus = ({
  jdUrl,
  candidateId,
  jobStatus = "APPLICATION_SUCCESSFUL",
  datePartiallyApplied,
}) => {
  const url = `${BASE_URL}/${subpath}/updateApplication`;

  return fetchAdapter(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: {
      jdUrl,
      encryptedPublicIdentifier: candidateId,
      updateObj: {
        jobStatus,
        datePartiallyApplied,
        source: EXTERNAL_JOB_APPLICATION_SOURCE.GPT_AutoFill,
      },
    },
  });
};

export const getAutoAppliedApplication = ({
  jdUrl,
  candidateId,
  jdUid,
  linkedInJobId,
  source = "other",
  jobId,
}) => {
  const url = `${BASE_URL}/${subpath}/getApplication`;

  return fetchAdapter(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
    },
    body: {
      jdUrl,
      encryptedPublicIdentifier: candidateId,
      jdUid,
      linkedInJobId,
      source,
      jobId,
    },
  });
};
