import {
  createJDApplication,
  updateAutoAppliedApplicationStatus,
} from "../api/externalJobApplication";
import { getAutoFillConfig } from "../autofills";
import { CS_CANDIDATE_ID } from "../constants/chromeStorageConstants";
import { EXTERNAL_JOB_APPLICATION_SOURCE } from "../constants/commonConstants";
import { EASY_APPLY_AUTOFILL_DOMAIN_URL_PATTERNS } from "../constants/urls";
import { chromeLocalStorage } from "./chromeStorage";

const toastStyles = (isError) => ({
  position: "fixed",
  top: "30px",
  left: "50%",
  padding: "1rem",
  backgroundColor: isError ? "rgba(234,38,23)" : "rgba(76,175,80)",
  color: " #fff",
  transform: "translate(-50%, 0)",
  animation: "fadein 0.5s",
  fontSize: "16px",
  zIndex: 2148372,
});

export const saveJDDetails = async ({
  jdUrl,
  source,
  jobId,
  jdUid,
  linkedInJobId,
  reApply = false,
} = {}) => {
  const imageUrl = document
    .querySelector("meta[property='og:image']")
    ?.getAttribute("content");
  const jobTitle = document
    .querySelector("meta[property='og:title']")
    ?.getAttribute("content");

  const candidateId = await chromeLocalStorage.getItemAsync(CS_CANDIDATE_ID);

  if (reApply) {
    await updateAutoAppliedApplicationStatus({
      candidateId,
      jdUrl,
      jobStatus: EXTERNAL_JOB_APPLICATION_SOURCE.PARTIAL_APPLICATION,
      datePartiallyApplied: true,
    });
    return { success: true };
  }

  return createJDApplication({
    candidateId,
    imageUrl,
    jobTitle,
    jdUrl,
    jdUid,
    linkedInJobId,
    jobId,
    source,
  });
};

export const addToast = ({ message, type, timeout = 5000 }) => {
  const div = document.createElement("div");
  div.innerHTML = message;
  Object.entries(toastStyles(type === "error")).forEach(([property, value]) => {
    div.style[property] = value;
  });
  setTimeout(function removeToast() {
    div.remove();
  }, message.timeout || timeout);
  document.body.appendChild(div);
};

export const getJobIdAndSourceFromUrl = (url) => {
  for (const [domain, urlPatterns] of Object.entries(
    EASY_APPLY_AUTOFILL_DOMAIN_URL_PATTERNS
  )) {
    for (const urlPattern of urlPatterns) {
      const match = url.match(urlPattern.regex);
      if (match && match?.[urlPattern.indexOfJobIdInUrl]) {
        return {
          source: domain,
          jobId: match?.[urlPattern.indexOfJobIdInUrl],
        };
      }
    }
  }
  return {
    source: "other",
    jobId: null,
  };
};

export const fetchIncompleteMandatoryFields = async () => {
  const { candidateData, remoteConfig } = await getAutoFillConfig();
  return (
    remoteConfig?.mandatoryFields
      ?.filter((field) => !candidateData[field.dbKey])
      ?.map((field) => field.dbKey) || []
  );
};
