import { openLinkedinPopup } from "../Background/browser";
// import { sendEvent } from "../analytics/analytics";
import { SCREEN_EVENTS } from "../analytics/event";
import {
  CS_LAST_401_ERROR,
  CS_SELF_PUBLIC_ID,
} from "../constants/chromeStorageConstants";
import { sendCustomErrorMessageToSentry } from "../sentry/utils";
import { chromeLocalStorage } from "../utils/chromeStorage";
import { DO_NOT_REDIRECT_TO_LINKEDIN_ON_FAILURE } from "./constants";
import { getUserProfile } from "./linkedin";

export const linkedFetchWrapper = async ({
  shouldRedirectToLinkedinOnFailure = true,
  fetchArgs,
}) => {
  const result = await fetch(...fetchArgs);

  const data = {
    url: typeof fetchArgs[0] === "string" ? fetchArgs[0] : fetchArgs[0]?.url,
    statusCode: result.status,
    publicIdentifier: await chromeLocalStorage.getItemAsync(CS_SELF_PUBLIC_ID),
  };
  // sendEvent(SCREEN_EVENTS.LINKEDIN_API_CALL, data);

  if (result.status >= 400 && result.status < 500) {
    // sendEvent(SCREEN_EVENTS.LINKEDIN_API_ERROR, data);
    if (![401, 403].includes(result.status)) {
      sendCustomErrorMessageToSentry(SCREEN_EVENTS.LINKEDIN_API_ERROR, data);
    }
  }

  if (result.ok) {
    return result;
  }

  // Unauthorized
  if (result.status >= 401 && result.status < 500) {
    let isAuthenticated = false;
    if (result.status === 403) {
      isAuthenticated = isLinkedinAuthenticated();
    }
    if (!isAuthenticated) {
      chromeLocalStorage.setItemAsync(CS_LAST_401_ERROR, Date.now());
    }
    // sendEvent(SCREEN_EVENTS.PAUSED_SYNCING_DUE_TO_4XX, {
    //   url: typeof fetchArgs[0] === "string" ? fetchArgs[0] : fetchArgs[0]?.url,
    //   statusCode: result.status,
    // });
    if (shouldRedirectToLinkedinOnFailure) {
      // sendEvent(SCREEN_EVENTS.REDIRECTED_TO_LINKEDIN_FOR_SIGNIN, {
      //   status: result.status,
      //   url:
      //     typeof fetchArgs[0] === "string" ? fetchArgs[0] : fetchArgs[0]?.url,
      // });
      openLinkedinPopup();
    }
    return Promise.reject({
      ...result,
      isAuthenticated,
      isClientError: true,
      success: false,
      message: "Not logged In",
      statusCode: result.status,
    });
  }
  return Promise.reject({
    ...result,
    isAuthenticated: true,
    isClientError: result.status >= 400 && result.status < 500,
    success: false,
    statusCode: result.status,
  });
};

export const isLinkedinAuthenticated = async () => {
  let authenticated = false;
  const selfPublicIdentifier = await chromeLocalStorage.getItemAsync(
    CS_SELF_PUBLIC_ID
  );
  if (!selfPublicIdentifier) return authenticated;
  try {
    await getUserProfile(
      selfPublicIdentifier,
      DO_NOT_REDIRECT_TO_LINKEDIN_ON_FAILURE
    );
    authenticated = true;
  } catch (e) {
    authenticated = false;
  }
  return authenticated;
};
