import { REMOTE_CONFIG_KEYS } from "../constants/commonConstants";
import { BASE_URL } from "./constants";
import fetchAdapter from "./fetchAdapter";

const subpath = "internalServices";

const getFirebaseRemoteConfigObj = (allowedKeys = REMOTE_CONFIG_KEYS) => {
  return fetchAdapter(`${BASE_URL}/${subpath}/adhoc/getRemoteConfigObj`, {
    method: "POST",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
    },
    body: {
      allowedKeys,
    },
  }).then((remoteConfig) => remoteConfig?.REMOTE_CONFIG);
};

export { getFirebaseRemoteConfigObj };
