import React, { useEffect, useState } from "react";
import "./styles.css";

const Circle = ({ colour, percentage, radius }) => {
  const r = 0.7 * radius;
  const circ = 2 * Math.PI * r;
  const strokePct = ((100 - percentage) * circ) / 100;
  return (
    <circle
      className="circle"
      r={r}
      cx={radius}
      cy={radius}
      fill="transparent"
      stroke={strokePct !== circ ? colour : ""} // remove colour as 0% sets full circumference
      strokeWidth={4}
      strokeDasharray={circ}
      strokeDashoffset={percentage ? strokePct : 0}
      strokeLinecap="round"
    ></circle>
  );
};

const Text = ({ percentage, textColor }) => {
  return (
    <text
      x="50%"
      y="50%"
      dominantBaseline="central"
      textAnchor="middle"
      fontSize={"11px"}
      fill={textColor}
      className="percentage-text"
    >
      {percentage.toFixed(0)}%
    </text>
  );
};

const Percentage = ({
  percentage,
  radius,
  backgroundColor = "#c4c4c4",
  mainColor = "#fff",
  textColor = "#fff",
}) => {
  const [currentPercentage, setCurrentPercentage] = useState(1);

  useEffect(() => {
    setTimeout(() => {
      setCurrentPercentage(percentage);
    }, 1000);
  }, [percentage]);

  return (
    <svg width={radius * 2} height={radius * 2}>
      <g transform={`rotate(-90 ${radius} ${radius})`}>
        <Circle colour={backgroundColor} radius={radius} />
        <Circle
          colour={mainColor}
          percentage={currentPercentage}
          radius={radius}
        />
      </g>
      <Text percentage={percentage} textColor={textColor} />
    </svg>
  );
};

export default Percentage;
