import { sendEvent } from "../analytics/analytics";
import { SCREEN_EVENTS } from "../analytics/event";
import { getFirebaseRemoteConfigObj } from "../api/internalServices";
import { fetchCandidateByIdentifier } from "../api/user";
import { CS_CANDIDATE_ID } from "../constants/chromeStorageConstants";
import {
  GET_CURRENT_TAB_URL,
  SHOW_TOAST_ON_WEBSITE,
} from "../constants/messageConstants";
import {
  AUTO_APPLY_CUSTOM_URL_QUERY_PARAM,
  AUTO_FILL_DOMAIN,
} from "../constants/urls";
import { saveJDDetails } from "../utils/autoApply";
import {
  isOpenedInIframe,
  queryStringParse,
  removeQueryParamFromUrl,
} from "../utils/browser";
import { chromeLocalStorage } from "../utils/chromeStorage";
import { CACHE_CONFIG, getCachedResponse } from "../utils/cacheApiResponse";
import { incrementAppliedApplications } from "../utils/common";
import GPTAutoFill from "./GPTAutoFill";
import ashbyhqAutoFill from "./ashbyhqAutoFill";

const NOT_AN_APPLICATION_PAGE = "It doesn't seems like an application page";

const getAutoFillConfig = async () => {
  const candidateId = await chromeLocalStorage.getItemAsync(CS_CANDIDATE_ID);
  const [candidateData, remoteConfig] = await Promise.all([
    getCachedResponse(
      () => fetchCandidateByIdentifier(candidateId),
      CACHE_CONFIG.FETCH_CANDIDATE_DATA
    ),
    getCachedResponse(getFirebaseRemoteConfigObj, CACHE_CONFIG.REMOTE_CONFIG),
  ]);

  return {
    candidateData,
    remoteConfig: JSON.parse(
      remoteConfig?.["auto_apply_parsers"]?.defaultValue?.value
    ),
    candidateId,
  };
};

const addEntryInDbAndNotifyUser = async ({
  jdUrl,
  jobId,
  source,
  reApply = false,
}) => {
  const url =
    jdUrl ||
    (isOpenedInIframe()
      ? await chrome.runtime.sendMessage({
          message: GET_CURRENT_TAB_URL,
        })
      : window.location.href);
  if (!url) return;

  const jdUid = queryStringParse(new URL(url)?.search)?.[
    AUTO_APPLY_CUSTOM_URL_QUERY_PARAM.weekdayJdUid
  ];
  const linkedInJobId = queryStringParse(new URL(url)?.search)?.[
    AUTO_APPLY_CUSTOM_URL_QUERY_PARAM.linkedInJobId
  ];

  const careerPageUrl = removeQueryParamFromUrl(
    url,
    Object.values(AUTO_APPLY_CUSTOM_URL_QUERY_PARAM)
  )?.split("#")[0];

  try {
    const { success } = await saveJDDetails({
      jdUrl: careerPageUrl,
      jobId,
      source,
      jdUid,
      linkedInJobId,
      reApply,
    });

    if (success) {
      await incrementAppliedApplications(1);
    }
  } catch (e) {}
};

const resumeUploadFinished = (checkIfFinished) => {
  let timerId, intervalId;
  return new Promise((res) => {
    intervalId = setInterval(() => {
      if (checkIfFinished()) {
        clearTimeout(timerId);
        clearInterval(intervalId);
        res("done");
      }
    }, 1000); // check every 1 sec
    timerId = setTimeout(() => {
      res("timeout");
      clearTimeout(timerId);
      clearInterval(intervalId);
    }, 10 * 1000); // max time limit of 10 sec
  });
};

const handlePartialAutoApply = async ({
  totalFields,
  totalFieldsLength,
  autoFilledFields,
  autoFilledFieldsLength,
  fieldsNotFilled,
  autoApplyFormSelector,
  autoApplyResumeSelector,
  jdUrl,
  isPartialDomain,
  autoFilledFieldsWithEmptyValues,
  source,
}) => {
  await incrementAppliedApplications(1);
  sendEvent(SCREEN_EVENTS.AUTO_APPLY_FILL_FORM_SUCCESS, {
    totalFields,
    totalFieldsLength,
    autoFilledFieldsLength,
    autoFilledFields,
    fieldsNotFilled,
    autoApplyFormSelector,
    autoApplyResumeSelector,
    jdUrl,
    isIframe: isOpenedInIframe(),
    isPartialDomain,
    autoFilledFieldsWithEmptyValues,
    source,
  });
  sendEvent(SCREEN_EVENTS.APPLIED_OR_REFERRAL_SUBMITTED, {
    jdUrl,
    isIframe: isOpenedInIframe(),
    from: "AI Apply Success",
  });
};

const guessAutoFillTypeFromDom = () => {
  if (
    document.querySelector(".phenom-form-container") ||
    document.querySelector("a[ph-tevent='apply_click']")
  )
    return AUTO_FILL_DOMAIN.PHENOM.label;

  if (
    document
      .querySelector("meta[property='og:image']")
      ?.content?.startsWith(
        "https://s3.amazonaws.com/files.freshteam.com/production"
      )
  )
    return AUTO_FILL_DOMAIN.FRESHTEAM.label;
};

const showInvalidPageErrorMsg = () => {
  const queryParams = queryStringParse();
  if (!queryParams?.[AUTO_APPLY_CUSTOM_URL_QUERY_PARAM.easyApplyOnLoad]) {
    chrome.runtime.sendMessage({
      message: SHOW_TOAST_ON_WEBSITE,
      data: {
        message: NOT_AN_APPLICATION_PAGE,
        type: "error",
        delay: 500,
      },
    });
  }
};

const getAutoApplyDomain = (url) => {
  const autoApplyDomains = Object.values(AUTO_FILL_DOMAIN);
  const hostname = new URL(url)?.hostname;
  for (const domain of autoApplyDomains) {
    if (domain.regex.test(hostname)) {
      return domain.label;
    }
  }
  return null;
};

const autoFillForm = (easyApplyDomain, chatGPTResponse, reApply) => {
  switch (easyApplyDomain) {
    case AUTO_FILL_DOMAIN.ASHBYHQ.label: {
      return ashbyhqAutoFill();
    }
    default:
      return GPTAutoFill(chatGPTResponse, reApply);
  }
};

export {
  NOT_AN_APPLICATION_PAGE,
  guessAutoFillTypeFromDom,
  showInvalidPageErrorMsg,
  getAutoApplyDomain,
  autoFillForm,
  getAutoFillConfig,
  addEntryInDbAndNotifyUser,
  handlePartialAutoApply,
  resumeUploadFinished,
};
