const createFile = async (url) => {
  const response = await fetch(url);
  const data = await response.blob();
  const metadata = {
    type: "application/pdf",
  };
  return new File([data], "resume.pdf", metadata);
};

export const uploadFileToInput = async (remoteResumeURL, input) => {
  const file = await createFile(remoteResumeURL);
  const dt = new DataTransfer();
  dt.items.add(file);
  input.files = dt.files;

  const event = new Event("change", {
    bubbles: true,
  });
  input.dispatchEvent(event);
};

export const uploadFileViaDragAndDrop = async (
  remoteResumeURL,
  dropContainer
) => {
  const file = await createFile(remoteResumeURL);
  const dataTransfer = new DataTransfer();
  dataTransfer.items.add(file);
  const dropEvent = new DragEvent("drop", {
    dataTransfer,
  });
  dropContainer.dispatchEvent(dropEvent);
};
